import React from 'react';
import * as CustomCode from '../CustomCode';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import { Button, ScreenContainer, withTheme } from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, Text, View } from 'react-native';

const PreviewCambioTurnoScreen = props => {
  const { theme, navigation } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const InitializeCurrentDate = () => {
    return CustomCode.InitializeCurrentDate(Constants);
  };
  const [currentDate, setCurrentDate] = React.useState(
    InitializeCurrentDate("''")
  );
  const [screenName, setScreenName] = React.useState('Cambio de Turno');
  const [workschedule, setWorkschedule] = React.useState('');
  const Date2UtcString = aDate => {
    if (aDate) {
      if (variables['logging'])
        console.log(
          `Date2UTCString(${aDate}), type: ${typeof aDate}, ISO: ${aDate.toISOString()}, Local: ${aDate.toLocaleDateString()}`
        );

      aDate.setHours(0);

      const UTCString = aDate.toISOString().split('T')[0];
      if (variables['logging']) console.log(`UTCString: ${UTCString}`);
      return UTCString;
    }
    return '';
  };

  const log = value => {
    if (true || variables['logging'])
      console.log(`log value: ${JSON.stringify(value)}`);
    return value;
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();
  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        const timesheetRslt = (
          await DcSSApi.timesheetGET(Constants, {
            date_from: Date2UtcString(currentDate),
            date_to: Date2UtcString(currentDate),
            emp_id: Constants['my_emp_id'],
          })
        )?.json;
        const workschedule =
          timesheetRslt?.[0].timesheet.tim[0].tim_workschedule;
        setWorkschedule(workschedule);
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer hasSafeArea={true} scrollable={false}>
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <View
        style={StyleSheet.applyWidth(
          { alignItems: 'center' },
          dimensions.width
        )}
      >
        <Image
          resizeMode={'contain'}
          source={Images.Image20210630121543}
          style={StyleSheet.applyWidth(
            { height: 250, opacity: 0.75, width: '100%' },
            dimensions.width
          )}
        />
      </View>

      <View
        style={StyleSheet.applyWidth(
          { alignContent: 'center', height: '20%' },
          dimensions.width
        )}
      >
        <View
          style={StyleSheet.applyWidth(
            {
              alignItems: 'center',
              backgroundColor: theme.colors.secondary,
              height: 45,
              justifyContent: 'center',
              marginBottom: 5,
              marginTop: 5,
            },
            dimensions.width
          )}
        >
          <Text
            style={StyleSheet.applyWidth(
              {
                color: theme.colors.background,
                fontFamily: 'System',
                fontSize: 18,
                fontWeight: '500',
              },
              dimensions.width
            )}
          >
            {'Turno Actual'}
          </Text>
        </View>

        <View
          style={StyleSheet.applyWidth(
            {
              alignItems: 'center',
              alignSelf: 'center',
              backgroundColor: theme.colors.verylight,
              height: '35%',
              justifyContent: 'center',
              marginTop: 10,
              width: '95%',
            },
            dimensions.width
          )}
        >
          <Text
            style={StyleSheet.applyWidth(
              {
                color: theme.colors.primary,
                fontFamily: 'System',
                fontSize: 20,
                fontWeight: '700',
                textAlign: 'center',
              },
              dimensions.width
            )}
          >
            {workschedule}
          </Text>
        </View>
      </View>

      <View
        style={StyleSheet.applyWidth(
          {
            justifyContent: 'center',
            marginBottom: 15,
            marginLeft: '3%',
            marginRight: '3%',
            marginTop: 15,
          },
          dimensions.width
        )}
      >
        {/* Button Solid */}
        <>
          {!!null ? null : (
            <Button
              icon={'MaterialIcons/published-with-changes'}
              onPress={() => {
                try {
                  navigation.navigate('SolicitudCambioDeTurnoScreen', {
                    readOnly: false,
                  });
                } catch (err) {
                  console.error(err);
                }
              }}
              style={StyleSheet.applyWidth(
                {
                  backgroundColor: theme.colors.primary,
                  borderRadius: 10,
                  color: theme.colors.background,
                  fontFamily: 'System',
                  fontWeight: '700',
                  marginLeft: '6%',
                  marginRight: '6%',
                  minHeight: 48,
                  textAlign: 'center',
                },
                dimensions.width
              )}
              title={'Cambiar Turno'}
            />
          )}
        </>
      </View>
    </ScreenContainer>
  );
};

export default withTheme(PreviewCambioTurnoScreen);
