import React from 'react';
import * as CustomCode from '../CustomCode';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as locDatePicker from '../custom-files/locDatePicker';
import Undef from '../global-functions/Undef';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import {
  Button,
  DatePicker,
  Picker,
  ScreenContainer,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, Text, View } from 'react-native';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';

const SolicitudRetiroAnticipadoScreen = props => {
  const { theme } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const [concept, setConcept] = React.useState(Undef());
  const [conceptsList, setConceptsList] = React.useState([]);
  const [dateFrom, setDateFrom] = React.useState(new Date());
  const [dateFromError, setDateFromError] = React.useState('');
  const [datePickerValue, setDatePickerValue] = React.useState(new Date());
  const [dateTo, setDateTo] = React.useState(new Date());
  const [dateToError, setDateToError] = React.useState('');
  const [ioAction, setIoAction] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [pickerValue, setPickerValue] = React.useState('');
  const [pickerValue2, setPickerValue2] = React.useState('');
  const [reason, setReason] = React.useState(Undef());
  const [reasonError, setReasonError] = React.useState('');
  const [reasonsList, setReasonsList] = React.useState([]);
  const [screenName, setScreenName] = React.useState('Retiro Anticipado');
  const now = () => {
    return new Date().toISOString();
  };

  const reasons2PickerOptions = reasons => {
    return reasons
      .filter(r => r.rea_type === 'rys')
      .map(r => {
        return { label: r.rea_name, value: r.rea_id };
      });
  };

  // Convierte un Date a un string YYYY-MM-DD
  const Date2IsoDateTimeString = aDate => {
    return CustomCode.Date2IsoDateTimeString(aDate);
  };

  const postResultToast = fetchResult => {
    return CustomCode.PostResultToast(
      fetchResult,
      screenName,
      'back',
      '',
      2000,
      props
    );
  };

  const trimSeconds = aDateTime => {
    aDateTime.setSeconds(0, 0);

    return aDateTime;
  };

  const initScreenVars = () => {
    setDateFrom(undefined);
    setDateTo(undefined);
    setDateFromError(undefined);
    setDateToError(undefined);
    setConcept(undefined);
    setReason(undefined);
  };

  const conceptsToPickerOptions = concepts => {
    // console.log(`concepts: ${JSON.stringify(concepts)}`);

    // const filtered = concepts.filter((c) => c.con_type === 'di');
    // console.log(`filtered: ${JSON.stringify(filtered)}`);

    // const mapped = filtered.map((c) => { return { label: c.con_name, value: c.con_id }; });
    // console.log(`mapped: ${JSON.stringify(mapped)}`);

    // return mapped;

    const options = concepts
      .filter(c => c.con_type === 'hs')
      .map(c => {
        return { label: c.con_name, value: c.con_id, ...c };
      });

    // const options = concepts
    //         .filter((c) => c.con_type === 'di')
    //         .map((c) => { return { label: c.con_name, value: c.con_id, ...c }; });

    // console.log(`options: ${JSON.stringify(options)}`);

    return options;
  };

  const validate = () => {
    /*
setDateFromError(undefined);
setDateToError(undefined);

// console.log(`validate: dateFrom: ${dateFrom}, dateTo: ${dateTo}`);

const minDate = new Date();
// minDate.setDate(1 + minDate.getDate());
// minDate.setHours(0,0,0,0);

if(dateFrom >= minDate)
if(dateTo >= dateFrom)
    return false;
else
    setDateToError("'Hasta' debe ser posterior a 'Desde'");
else
setDateFromError('No puede comenzar antes de ahora');

return true;
*/

    setDateFromError(undefined);
    setDateToError(undefined);

    console.log(
      `validate: dateFrom: ${dateFrom}, dateTo: ${dateTo}, conceptId: ${concept}`
    );

    const oConcept = conceptsList.find(c => c.con_id == concept);
    console.log(`validate: oConcept: ${JSON.stringify(oConcept)}`);

    var minDate = new Date();

    switch (oConcept?.con_apply_day) {
      case 1: // Lunes de esta semana
        var toMonday = minDate.getDay() - 1;
        if (toMonday < 0) toMonday = 6;
        minDate.setDate(minDate.getDate() - toMonday);
        break;

      case 2: // 1ro del mes en curso
        minDate.setDate(1);
        break;

      case 3: // 1/01 del ao en curso
        minDate.setDate(1);
        minDate.setMonth(0);
        break;

      case 0: // Hoy
      default:
        break;
    }

    minDate.setDate(minDate.getDate() + oConcept?.con_offset_day || 0);
    // minDate.setDate(1 + minDate.getDate());
    minDate.setHours(0, 0, 0, 0);

    // return true;    // Invalidate

    if (dateFrom >= minDate)
      if (dateTo >= dateFrom) return false;
      else setDateToError("'Hasta' debe ser mayor o igual 'Desde'");
    // setDateFromError(`Fecha 'Desde' no pueder anterior a ${minDate.toLocaleDateString()}`);
    else
      setDateFromError(
        `'Desde' debe ser mayor o igual a ${minDate.toLocaleDateString(
          'es-ar',
          { year: 'numeric', month: 'numeric', day: 'numeric' }
        )}`
      );

    return true;
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]

  // setVariable({ key: 'loading', value: true });
  const dcSSRequestRysPOST = DcSSApi.useRequestRysPOST();
  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        initScreenVars();
        const conceptsResponse = (
          await DcSSApi.conceptsByEmpGET(Constants, {
            emp_id: Constants['my_emp_id'],
          })
        )?.json;
        setConceptsList(conceptsToPickerOptions(conceptsResponse));
        const reasonsResponse = (
          await DcSSApi.reasonsByEmpGET(Constants, {
            emp_id: Constants['my_emp_id'],
          })
        )?.json;
        setReasonsList(reasons2PickerOptions(reasonsResponse));
        setDateFrom(props.route?.params?.dateFrom ?? false);
        setDateTo(props.route?.params?.dateTo ?? false);
        setConcept(props.route?.params?.concept ?? '');
        setReason(props.route?.params?.reason ?? '');
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer hasSafeArea={true} scrollable={false}>
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={ioAction}
      />

      <KeyboardAwareScrollView
        style={StyleSheet.applyWidth({ height: 200 }, dimensions.width)}
      >
        <View
          style={StyleSheet.applyWidth(
            { alignItems: 'center' },
            dimensions.width
          )}
        >
          <Image
            resizeMode={'contain'}
            source={Images.Image20210630121543}
            style={StyleSheet.applyWidth(
              { height: 220, opacity: 0.75, width: '100%' },
              dimensions.width
            )}
          />
        </View>

        <View
          style={StyleSheet.applyWidth(
            { borderRadius: 12, marginLeft: '3%', marginRight: '3%' },
            dimensions.width
          )}
        >
          <View
            style={StyleSheet.applyWidth(
              {
                alignItems: 'center',
                backgroundColor: theme.colors.secondary,
                height: 45,
                justifyContent: 'center',
                marginBottom: 5,
                marginTop: 5,
              },
              dimensions.width
            )}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.background,
                  fontFamily: 'System',
                  fontSize: 18,
                  fontWeight: '500',
                },
                dimensions.width
              )}
            >
              {'Datos de la Solicitud'}
            </Text>
          </View>

          <View
            style={StyleSheet.applyWidth(
              {
                marginBottom: 5,
                marginLeft: '3%',
                marginRight: '3%',
                marginTop: 5,
              },
              dimensions.width
            )}
          >
            <View>
              {/* pkrConcepto */}
              <>
                {props.route?.params?.readOnly ?? false ? null : (
                  <Picker
                    disabled={false}
                    label={'Concepto'}
                    leftIconMode={'inset'}
                    onValueChange={newPkrConceptoValue => {
                      try {
                        setConcept(newPkrConceptoValue);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    options={conceptsList}
                    placeholder={'Concepto de liquidacin'}
                    style={StyleSheet.applyWidth(
                      {
                        borderColor: theme.colors['Light'],
                        color: theme.colors['Light'],
                        fontSize: 16,
                      },
                      dimensions.width
                    )}
                    type={'solid'}
                    value={concept}
                  />
                )}
              </>
              {/* pkrConceptoRO */}
              <>
                {!(props.route?.params?.readOnly ?? false) ? null : (
                  <Picker
                    disabled={true}
                    label={'Concepto'}
                    leftIconMode={'inset'}
                    onValueChange={newPkrConceptoROValue => {
                      try {
                        setConcept(newPkrConceptoROValue);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    options={conceptsList}
                    placeholder={'Concepto'}
                    style={StyleSheet.applyWidth(
                      { color: theme.colors['Light'], fontSize: 16 },
                      dimensions.width
                    )}
                    type={'solid'}
                    value={concept}
                  />
                )}
              </>
              <Text
                numberOfLines={1}
                style={StyleSheet.applyWidth(
                  {
                    color: theme.colors.error,
                    fontSize: 10,
                    marginBottom: 1,
                    textAlign: 'center',
                  },
                  dimensions.width
                )}
              >
                {null}
              </Text>
            </View>

            <View
              style={StyleSheet.applyWidth({ marginTop: 5 }, dimensions.width)}
            >
              {/* pkrMotivo */}
              <>
                {props.route?.params?.readOnly ?? false ? null : (
                  <Picker
                    label={'Motivo'}
                    leftIconMode={'inset'}
                    onValueChange={newPkrMotivoValue => {
                      try {
                        setReason(newPkrMotivoValue);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    options={reasonsList}
                    placeholder={'Motivo del retiro/salida'}
                    style={StyleSheet.applyWidth(
                      {
                        borderColor: theme.colors['Light'],
                        color: theme.colors['Light'],
                        fontSize: 16,
                      },
                      dimensions.width
                    )}
                    type={'solid'}
                    value={reason}
                  />
                )}
              </>
              {/* pkrMotivoRO */}
              <>
                {!(props.route?.params?.readOnly ?? false) ? null : (
                  <Picker
                    disabled={true}
                    label={'Motivo'}
                    leftIconMode={'inset'}
                    onValueChange={newPkrMotivoROValue => {
                      try {
                        setReason(newPkrMotivoROValue);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    options={reasonsList}
                    placeholder={'Motivo del retiro/salida'}
                    style={StyleSheet.applyWidth(
                      { color: theme.colors['Light'], fontSize: 16 },
                      dimensions.width
                    )}
                    type={'solid'}
                    value={reason}
                  />
                )}
              </>
              <>
                {!reasonError ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {reasonError}
                  </Text>
                )}
              </>
            </View>

            <View
              style={StyleSheet.applyWidth({ marginTop: 5 }, dimensions.width)}
            >
              {/* ccDateTimePkrFrom */}
              <>
                {!!(props.route?.params?.readOnly ?? false) ? null : (
                  <Utils.CustomCodeErrorBoundary>
                    <locDatePicker.LocDatePicker
                      theme={theme}
                      label="Desde"
                      mode="datetime"
                      format="dd/mm/yyyy HH:MM"
                      locale="es"
                      date={dateFrom}
                      onValueChange={setDateFrom}
                    />
                  </Utils.CustomCodeErrorBoundary>
                )}
              </>
              {/* ccDateTimePkrFromRO */}
              <>
                {!(props.route?.params?.readOnly ?? false) ? null : (
                  <Utils.CustomCodeErrorBoundary>
                    <locDatePicker.LocDatePicker
                      disabled={true}
                      theme={theme}
                      label="Desde"
                      mode="datetime"
                      format="dd/mm/yyyy HH:MM"
                      locale="es"
                      date={dateFrom}
                    />
                  </Utils.CustomCodeErrorBoundary>
                )}
              </>
              <>
                {!dateFromError ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {dateFromError}
                  </Text>
                )}
              </>
            </View>

            <View
              style={StyleSheet.applyWidth({ marginTop: 5 }, dimensions.width)}
            >
              {/* ccDateTimePkrTo */}
              <>
                {!!(props.route?.params?.readOnly ?? false) ? null : (
                  <Utils.CustomCodeErrorBoundary>
                    <locDatePicker.LocDatePicker
                      theme={theme}
                      label="Hasta"
                      mode="datetime"
                      format="dd/mm/yyyy HH:MM"
                      locale="es"
                      date={dateTo}
                      onValueChange={setDateTo}
                    />
                  </Utils.CustomCodeErrorBoundary>
                )}
              </>
              {/* ccDateTimePkrToRO */}
              <>
                {!(props.route?.params?.readOnly ?? false) ? null : (
                  <Utils.CustomCodeErrorBoundary>
                    <locDatePicker.LocDatePicker
                      disabled={true}
                      theme={theme}
                      label="Hasta"
                      mode="datetime"
                      format="dd/mm/yyyy HH:MM"
                      locale="es"
                      date={dateTo}
                    />
                  </Utils.CustomCodeErrorBoundary>
                )}
              </>
              <>
                {!dateToError ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {dateToError}
                  </Text>
                )}
              </>
            </View>
          </View>
        </View>

        <View
          style={StyleSheet.applyWidth(
            {
              justifyContent: 'center',
              marginBottom: 15,
              marginLeft: '3%',
              marginRight: '3%',
              marginTop: 15,
            },
            dimensions.width
          )}
        >
          {/* Button Solid */}
          <>
            {!!(props.route?.params?.readOnly ?? false) ? null : (
              <Button
                onPress={() => {
                  const handler = async () => {
                    try {
                      const validateError = validate();
                      if (validateError) {
                        return;
                      }
                      setIoAction('saving');
                      setLoading(true);
                      const fetchResult = (
                        await dcSSRequestRysPOST.mutateAsync({
                          emp_id: Constants['my_emp_id'],
                          req_datetime: now(),
                          req_rys_con_id: parseInt(concept, 10),
                          req_rys_from: Date2IsoDateTimeString(dateFrom),
                          req_rys_rea_id: parseInt(reason, 10),
                          req_rys_to: Date2IsoDateTimeString(dateTo),
                        })
                      )?.json;
                      setLoading(false);
                      const isError = postResultToast(fetchResult);
                      if (isError) {
                        return;
                      }
                    } catch (err) {
                      console.error(err);
                    }
                  };
                  handler();
                }}
                style={StyleSheet.applyWidth(
                  {
                    backgroundColor: theme.colors.primary,
                    borderRadius: 10,
                    color: theme.colors.background,
                    fontFamily: 'System',
                    fontWeight: '700',
                    marginLeft: '6%',
                    marginRight: '6%',
                    minHeight: 48,
                    textAlign: 'center',
                  },
                  dimensions.width
                )}
                title={'Solicitar'}
              />
            )}
          </>
        </View>
      </KeyboardAwareScrollView>
    </ScreenContainer>
  );
};

export default withTheme(SolicitudRetiroAnticipadoScreen);
