import React from 'react';
import * as CustomCode from '../CustomCode';
import * as GlobalStyles from '../GlobalStyles.js';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import { Button, DatePicker, ScreenContainer, withTheme } from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, Text, View } from 'react-native';

const SolicitudVacacionesScreen = props => {
  const { theme } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const [dateFrom, setDateFrom] = React.useState(new Date());
  const [dateFromError, setDateFromError] = React.useState('');
  const [dateTo, setDateTo] = React.useState(new Date());
  const [dateToError, setDateToError] = React.useState('');
  const [ioAction, setIoAction] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [reqQuotas, setReqQuotas] = React.useState([]);
  const [screenName, setScreenName] = React.useState('Vacaciones');
  const [vacLimit, setVacLimit] = React.useState(-1);
  const now = () => {
    return new Date().toISOString();
  };

  const postResultToast = fetchResult => {
    return CustomCode.PostResultToast(
      fetchResult,
      screenName,
      'back',
      '',
      2000,
      props
    );
  };

  const validate = () => {
    setDateFromError(undefined);
    setDateToError(undefined);

    // console.log(`validate: dateFrom: ${dateFrom}, dateTo: ${dateTo}`);

    const minDate = new Date();
    minDate.setDate(1 + minDate.getDate());
    minDate.setHours(0, 0, 0, 0);

    if (dateFrom >= minDate)
      if (dateTo >= dateFrom) return false;
      else setDateToError("'Hasta' debe ser posterior a 'Desde'");
    else setDateFromError('No puede comenzar antes de maana');

    return true;
  };

  // Convierte un Date a un string YYYY-MM-DD
  const Date2IsoDateString = aDate => {
    return CustomCode.Date2IsoDateString(aDate);
  };

  const calcReqQuota = reqQuotas => {
    const VACACIONES_CONCEPT_ID = 70;

    const vLimit = reqQuotas.find(r => r.con_id == VACACIONES_CONCEPT_ID);

    console.log(
      `calcReqQuota(${VACACIONES_CONCEPT_ID}) -> ${vLimit}, amount: ${vLimit?.con_amount}`
    );

    if (vLimit?.con_amount > 0) setVacLimit(vLimit.con_amount);
    else setVacLimit(-1);
  };

  const initScreenVars = () => {
    // console.log("initScreenVars init");
    setDateFrom(undefined);
    setDateTo(undefined);
    setDateFromError(undefined);
    setDateToError(undefined);
    // setConcept(undefined);
    // setReason(undefined);
    // console.log("initScreenVars end");
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]

  // setVariable({ key: 'loading', value: true });
  const dcSSRequestVacPOST = DcSSApi.useRequestVacPOST();
  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        initScreenVars();
        setDateFrom(props.route?.params?.dateFrom ?? false);
        setDateTo(props.route?.params?.dateTo ?? false);
        const aQuotas = (
          await DcSSApi.requestQuotaGET(Constants, {
            emp_id: Constants['my_emp_id'],
          })
        )?.json;
        setReqQuotas(aQuotas);
        calcReqQuota(aQuotas);
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer hasSafeArea={true} scrollable={false}>
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={ioAction}
      />

      <View
        style={StyleSheet.applyWidth(
          { alignItems: 'center' },
          dimensions.width
        )}
      >
        <Image
          resizeMode={'contain'}
          source={Images.UndrawAircraftFbvl}
          style={StyleSheet.applyWidth(
            { height: 250, opacity: 0.75, width: '100%' },
            dimensions.width
          )}
        />
      </View>

      <View
        style={StyleSheet.applyWidth(
          { borderRadius: 12, marginLeft: '3%', marginRight: '3%' },
          dimensions.width
        )}
      >
        <View
          style={StyleSheet.applyWidth(
            {
              alignItems: 'center',
              backgroundColor: theme.colors.secondary,
              height: 45,
              justifyContent: 'center',
              marginBottom: 5,
              marginTop: 5,
            },
            dimensions.width
          )}
        >
          <Text
            style={StyleSheet.applyWidth(
              {
                color: theme.colors.background,
                fontFamily: 'System',
                fontSize: 18,
                fontWeight: '500',
              },
              dimensions.width
            )}
          >
            {'Perodo Solicitado'}
          </Text>
        </View>
        {/* vacLimitVw */}
        <>
          {!(vacLimit >= 0) ? null : (
            <View
              style={StyleSheet.applyWidth(
                {
                  flexDirection: 'row',
                  justifyContent: 'center',
                  marginBottom: 5,
                  marginTop: 10,
                },
                dimensions.width
              )}
            >
              <Text
                accessible={true}
                allowFontScaling={true}
                style={StyleSheet.applyWidth(
                  StyleSheet.compose(GlobalStyles.TextStyles(theme)['Text'], {
                    color: theme.colors['Primary'],
                    fontFamily: 'System',
                    fontWeight: '400',
                  }),
                  dimensions.width
                )}
              >
                {'Ud. dispone de'}
              </Text>
              {/* Text 2 */}
              <Text
                accessible={true}
                allowFontScaling={true}
                style={StyleSheet.applyWidth(
                  StyleSheet.compose(GlobalStyles.TextStyles(theme)['Text'], {
                    color: theme.colors['Secondary'],
                    fontFamily: 'System',
                    fontWeight: '700',
                  }),
                  dimensions.width
                )}
              >
                {' '}
                {vacLimit}
                {' das '}
              </Text>
              {/* Text 3 */}
              <Text
                accessible={true}
                allowFontScaling={true}
                style={StyleSheet.applyWidth(
                  StyleSheet.compose(GlobalStyles.TextStyles(theme)['Text'], {
                    color: theme.colors['Primary'],
                    fontFamily: 'System',
                    fontWeight: '400',
                  }),
                  dimensions.width
                )}
              >
                {'de vacaciones'}
              </Text>
            </View>
          )}
        </>
        <View
          style={StyleSheet.applyWidth(
            {
              marginBottom: 5,
              marginLeft: '3%',
              marginRight: '3%',
              marginTop: 5,
            },
            dimensions.width
          )}
        >
          <View>
            {/* dpFrom */}
            <>
              {!!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  date={dateFrom}
                  disabled={false}
                  label={'Desde'}
                  leftIconMode={'inset'}
                  mode={'date'}
                  onDateChange={newDpFromValue => {
                    try {
                      setDateFrom(newDpFromValue);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={StyleSheet.applyWidth(
                    { marginTop: 5 },
                    dimensions.width
                  )}
                  type={'solid'}
                />
              )}
            </>
            {/* dpFromRO */}
            <>
              {!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  date={dateFrom}
                  disabled={true}
                  label={'Desde'}
                  leftIconMode={'inset'}
                  mode={'date'}
                  style={StyleSheet.applyWidth(
                    { marginTop: 5 },
                    dimensions.width
                  )}
                  type={'solid'}
                />
              )}
            </>
            <>
              {!dateFromError ? null : (
                <Text
                  numberOfLines={1}
                  style={StyleSheet.applyWidth(
                    {
                      color: theme.colors.error,
                      fontSize: 10,
                      marginBottom: 1,
                      textAlign: 'center',
                    },
                    dimensions.width
                  )}
                >
                  {dateFromError}
                </Text>
              )}
            </>
          </View>

          <View>
            {/* dpTo */}
            <>
              {!!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  label={'Hasta'}
                  leftIconMode={'inset'}
                  mode={'date'}
                  onDateChange={newDpToValue => {
                    try {
                      setDateTo(newDpToValue);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={StyleSheet.applyWidth(
                    { marginTop: 5 },
                    dimensions.width
                  )}
                  type={'solid'}
                />
              )}
            </>
            {/* dpToRO */}
            <>
              {!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  date={dateTo}
                  disabled={true}
                  label={'Hasta'}
                  leftIconMode={'inset'}
                  mode={'date'}
                  style={StyleSheet.applyWidth(
                    { marginTop: 5 },
                    dimensions.width
                  )}
                  type={'solid'}
                />
              )}
            </>
            <>
              {!dateToError ? null : (
                <Text
                  numberOfLines={1}
                  style={StyleSheet.applyWidth(
                    {
                      color: theme.colors.error,
                      fontSize: 10,
                      marginBottom: 1,
                      textAlign: 'center',
                    },
                    dimensions.width
                  )}
                >
                  {dateToError}
                </Text>
              )}
            </>
          </View>
        </View>
      </View>

      <View
        style={StyleSheet.applyWidth(
          {
            justifyContent: 'center',
            marginLeft: '3%',
            marginRight: '3%',
            marginTop: 25,
          },
          dimensions.width
        )}
      >
        {/* Button Solid */}
        <>
          {!!(props.route?.params?.readOnly ?? false) ? null : (
            <Button
              onPress={() => {
                const handler = async () => {
                  try {
                    const validateError = validate();
                    if (validateError) {
                      return;
                    }
                    setIoAction('saving');
                    setLoading(true);
                    const fetchResult = (
                      await dcSSRequestVacPOST.mutateAsync({
                        emp_id: Constants['my_emp_id'],
                        req_datetime: now(),
                        req_vac_from: Date2IsoDateString(dateFrom),
                        req_vac_to: Date2IsoDateString(dateTo),
                      })
                    )?.json;
                    setLoading(false);
                    const isError = postResultToast(fetchResult);
                  } catch (err) {
                    console.error(err);
                  }
                };
                handler();
              }}
              style={StyleSheet.applyWidth(
                {
                  backgroundColor: theme.colors.primary,
                  borderRadius: 10,
                  color: theme.colors.background,
                  fontFamily: 'System',
                  fontWeight: '700',
                  marginLeft: '6%',
                  marginRight: '6%',
                  minHeight: 48,
                  textAlign: 'center',
                },
                dimensions.width
              )}
              title={'Solicitar'}
            />
          )}
        </>
      </View>
    </ScreenContainer>
  );
};

export default withTheme(SolicitudVacacionesScreen);
