import React from 'react';
import * as CustomCode from '../CustomCode';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import {
  Icon,
  IconButton,
  ScreenContainer,
  Surface,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { Image, ScrollView, Text, View } from 'react-native';

const ConsultasScreen = props => {
  const { theme, navigation } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const [screenName, setScreenName] = React.useState('Consultas');
  const renderMenuItems = items => {
    const styles = StyleSheet.create({
      OutterView: {
        marginTop: 5,
        marginBottom: 5,
      },
      SurfaceVw: {
        minHeight: 120,
      },
      OutterItemVw: {
        justifyContent: 'space-evenly',
        position: 'absolute',
        width: '100%',
        height: '100%',
        marginTop: 5,
        marginBottom: 5,
      },
      Image: {
        width: '100%',
        height: '100%',
        position: 'absolute',
        opacity: 0.2,
      },
      InnerIconVw: {
        marginLeft: 15,
      },
      InnerText: {
        fontFamily: 'System',
        fontWeight: '600',
        fontSize: 32,
      },
      InnerTextVw: {
        alignItems: 'flex-end',
        marginRight: 15,
      },
    });

    const renderItem = item => {
      console.log(`renderItem(${item.name}, [${item.icon}])`);

      try {
        return (
          <View style={styles.OutterView}>
            <Surface
              style={[
                styles.SurfaceVw,
                { backgroundColor: theme.colors.medium, borderRadius: 8 },
              ]}
            >
              <Image
                style={styles.Image}
                source={item.background}
                resizeMode={'cover'}
              />
              <View style={styles.OutterItemVw}>
                <View style={styles.InnerIconVw}>
                  <Icon
                    name={item.icon}
                    size={48}
                    color={
                      item.enabled
                        ? theme.colors.background
                        : theme.colors.mediumInverse
                    }
                  />
                </View>

                <View style={styles.InnerTextVw}>
                  <Text
                    style={[
                      styles.InnerText,
                      {
                        color: item.enabled
                          ? theme.colors.background
                          : theme.colors.mediumInverse,
                      },
                    ]}
                  >
                    {item.name}
                  </Text>
                </View>
              </View>
            </Surface>
          </View>
        );
      } catch (e) {
        console.log(`error: ${e} `);
      }
    };

    const renderTouchableItem = item => {
      try {
        return (
          <Touchable
            onPress={() => {
              try {
                navigation.navigate(item.target);
              } catch (err) {
                console.error(err);
              }
            }}
          >
            {renderItem(item)}
          </Touchable>
        );
      } catch (e) {
        console.log(`renderTouchableItem error: ${e} `);
      }
    };

    return items
      ?.filter(i => i.visible)
      .map(i => (i.enabled ? renderTouchableItem(i) : renderItem(i)));
  };

  const menuItems = () => {
    return [
      {
        name: 'Fichadas',
        icon: 'MaterialCommunityIcons/calendar-blank-multiple',
        background: Images.UndrawRequest,
        target: 'ConsultaFichadasScreen',
        visible: true,
        enabled: true,
        permission: 'Consultas:Fichadas',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
      {
        name: 'Horas Extras',
        icon: 'MaterialCommunityIcons/briefcase-clock-outline',
        background: Images.UndrawWorking,
        target: 'ConsultaHorasExtrasScreen',
        visible: true,
        enabled: true,
        permission: 'Consultas:HorasExtras',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
      {
        name: 'Solicitudes',
        icon: 'FontAwesome/pencil-square-o',
        background: Images.UndrawRequest01,
        target: 'ConsultaSolicitudesScreen',
        visible: true,
        enabled: true,
        permission: 'Consultas:Solicitudes',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
      {
        name: 'Comedor',
        icon: 'Feather/coffee',
        background: Images.UndrawLunch,
        target: 'ConsultaComedorScreen',
        visible: true,
        enabled: true,
        permission: 'Consultas:Comedor',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
      {
        name: 'Documentos',
        icon: 'FontAwesome/envelope-o',
        background: Images.UndrawOpenedReI38e,
        target: 'ConsultaDocumentosScreen',
        visible: true,
        enabled: true,
        permission: 'Consultas:Documentos',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
      // {
      //     name:   "Presentismo",
      //     icon:   "SimpleLineIcons/trophy",
      //     background: Images.UndrawWinnersAo2o,
      //     target: 'ConsultaPresentismoScreen',
      //     visible: false,
      //     enabled: true,
      //     permission: 'Consultas:Presentismo'
      // }
    ];
  };

  return (
    <ScreenContainer
      hasSafeArea={true}
      hasTopSafeArea={true}
      scrollable={false}
    >
      <CustomCode.DigicardScreenHeader
        {...props}
        right={{
          icon: 'Ionicons/home-outline',
          nav: 'Main',
          screen: 'InicioScreen',
        }}
        title={screenName}
      />
      <View
        style={StyleSheet.applyWidth(
          {
            flex: 1,
            marginBottom: 10,
            marginLeft: 12,
            marginRight: 12,
            marginTop: 15,
            paddingBottom: 25,
          },
          dimensions.width
        )}
      >
        <Utils.CustomCodeErrorBoundary>
          {CustomCode.renderMenuItems(
            menuItems(),
            Constants['AppPermissions'],
            props
          )}
        </Utils.CustomCodeErrorBoundary>
      </View>
    </ScreenContainer>
  );
};

export default withTheme(ConsultasScreen);
