import React from 'react';
import * as CustomCode from '../CustomCode';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as locDatePicker from '../custom-files/locDatePicker';
import Undef from '../global-functions/Undef';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import {
  Button,
  DatePicker,
  Picker,
  ScreenContainer,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, Text, View } from 'react-native';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';

const SolicitudCambioDeTurnoScreen = props => {
  const { theme } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const [dateFrom, setDateFrom] = React.useState(new Date());
  const [dateFromError, setDateFromError] = React.useState('');
  const [datePickerValue, setDatePickerValue] = React.useState(new Date());
  const [dateTo, setDateTo] = React.useState(new Date());
  const [dateToError, setDateToError] = React.useState('');
  const [ioAction, setIoAction] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [reason, setReason] = React.useState(Undef());
  const [reasonError, setReasonError] = React.useState('');
  const [reasonsList, setReasonsList] = React.useState([]);
  const [screenName, setScreenName] = React.useState('Cambio de Turno');
  const [worksch, setWorksch] = React.useState(Undef());
  const [workschError, setWorkschError] = React.useState('');
  const [workschedulesList, setWorkschedulesList] = React.useState([]);
  const initScreenVars = () => {
    setDateFrom(undefined);
    setDateTo(undefined);
    setDateFromError(undefined);
    setDateToError(undefined);
    setWorksch(undefined);
    setReason(undefined);
  };

  // Convierte un Date a un string YYYY-MM-DD
  const Date2IsoDateString = aDate => {
    console.log(`Date2IsoDateString(${aDate})`);

    var offset = new Date().getTimezoneOffset() * 60 * 1000;

    const rslt = new Date(aDate - offset).toISOString().split('T')[0];

    console.log(`Date2IsoDateString(${aDate}): ${rslt}`);

    return rslt;
  };

  const reasons2PickerOptions = reasons => {
    return reasons
      .filter(r => r.rea_type === 'ctu')
      .map(r => {
        return { label: r.rea_name, value: r.rea_id };
      });
  };

  const workschedulesToPickerOptions = wscheds => {
    return wscheds.map(w => {
      return { label: w.wor_name, value: w.wor_id };
    });
  };

  // Extrae el Id de un array de picker options
  const extractId = opt => {
    console.log(`extractId(${opt})`);

    return opt.value;
  };

  const logVar = aVar => {
    // if(variables['logging'])
    console.log(`log value: ${JSON.stringify(aVar)}`);
    return aVar;
  };

  const now = () => {
    return new Date().toISOString();
  };

  const Date2UTCString = aDate => {
    console.log(
      `Date2UTCString(${aDate}), type: ${typeof aDate}, ISO: ${aDate.toISOString()}, Local: ${aDate.toLocaleDateString()}`
    );

    aDate.setHours(0);

    const UTCString = aDate.toISOString().split('T')[0] + 'T00:00:00.000Z';
    console.log(`UTCString: ${UTCString}`);
    return UTCString;
  };

  const validate = () => {
    setDateFromError(undefined);
    setDateToError(undefined);

    // console.log(`validate: dateFrom: ${dateFrom}, dateTo: ${dateTo}`);

    const minDate = new Date();
    minDate.setDate(1 + minDate.getDate());
    minDate.setHours(0, 0, 0, 0);

    if (dateFrom >= minDate)
      if (dateTo >= dateFrom) return false;
      else setDateToError("'Hasta' debe ser posterior a 'Desde'");
    else setDateFromError('No puede comenzar antes de maana');

    return true;
  };

  const delayNav = (delay, nav, screen) => {
    setTimeout(function () {
      if (nav === 'back') props.navigation.goBack();
      else props.navigation.navigate(nav, { screen: screen });
    }, delay);
  };

  const postResultToast = fetchResult => {
    return CustomCode.PostResultToast(
      fetchResult,
      screenName,
      'back',
      '',
      2000,
      props
    );
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]

  // setVariable({ key: 'loading', value: true });
  const dcSSRequestCtuPOST = DcSSApi.useRequestCtuPOST();
  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        initScreenVars();
        const workschedulesResponse = (
          await DcSSApi.workschedulesByEmpGET(Constants, {
            emp_id: Constants['my_emp_id'],
          })
        )?.json;
        setWorkschedulesList(
          workschedulesToPickerOptions(workschedulesResponse)
        );
        const reasonsResponse = (
          await DcSSApi.reasonsByEmpGET(Constants, {
            emp_id: Constants['my_emp_id'],
          })
        )?.json;
        setReasonsList(reasons2PickerOptions(reasonsResponse));
        setWorksch(props.route?.params?.workschedule ?? '');
        setReason(props.route?.params?.reason ?? '');
        setDateFrom(props.route?.params?.dateFrom ?? false);
        setDateTo(props.route?.params?.dateTo ?? false);
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer hasSafeArea={true} scrollable={false}>
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={ioAction}
      />

      <KeyboardAwareScrollView
        style={StyleSheet.applyWidth({ height: 200 }, dimensions.width)}
      >
        <View
          style={StyleSheet.applyWidth(
            { alignItems: 'center' },
            dimensions.width
          )}
        >
          <Image
            resizeMode={'contain'}
            source={Images.Image20210630121543}
            style={StyleSheet.applyWidth(
              { height: 220, opacity: 0.75, width: '100%' },
              dimensions.width
            )}
          />
        </View>

        <View
          style={StyleSheet.applyWidth(
            { borderRadius: 12, marginLeft: '3%', marginRight: '3%' },
            dimensions.width
          )}
        >
          <View
            style={StyleSheet.applyWidth(
              {
                alignItems: 'center',
                backgroundColor: theme.colors.secondary,
                height: 45,
                justifyContent: 'center',
                marginBottom: 5,
                marginTop: 5,
              },
              dimensions.width
            )}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.background,
                  fontFamily: 'System',
                  fontSize: 18,
                  fontWeight: '500',
                },
                dimensions.width
              )}
            >
              {'Turno Solicitado'}
            </Text>
          </View>

          <View
            style={StyleSheet.applyWidth(
              {
                marginBottom: 5,
                marginLeft: '3%',
                marginRight: '3%',
                marginTop: 5,
              },
              dimensions.width
            )}
          >
            <View>
              {/* pkrTurno */}
              <>
                {props.route?.params?.readOnly ?? false ? null : (
                  <Picker
                    label={'Turno'}
                    leftIconMode={'inset'}
                    onValueChange={newPkrTurnoValue => {
                      try {
                        setWorksch(newPkrTurnoValue);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    options={workschedulesList}
                    placeholder={'Nuevo turno'}
                    type={'solid'}
                    value={worksch}
                  />
                )}
              </>
              {/* pkrTurnoRO */}
              <>
                {!(props.route?.params?.readOnly ?? false) ? null : (
                  <Picker
                    disabled={true}
                    label={'Turno'}
                    leftIconMode={'inset'}
                    onValueChange={newPkrTurnoROValue => {
                      try {
                        setWorksch(newPkrTurnoROValue);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    options={workschedulesList}
                    placeholder={'Nuevo turno'}
                    type={'solid'}
                    value={worksch}
                  />
                )}
              </>
              <>
                {!workschError ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {workschError}
                  </Text>
                )}
              </>
            </View>

            <View
              style={StyleSheet.applyWidth({ marginTop: 5 }, dimensions.width)}
            >
              {/* pkrMotivo */}
              <>
                {props.route?.params?.readOnly ?? false ? null : (
                  <Picker
                    label={'Motivo'}
                    leftIconMode={'inset'}
                    onValueChange={newPkrMotivoValue => {
                      try {
                        setReason(newPkrMotivoValue);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    options={reasonsList}
                    placeholder={'Motivo del cambio'}
                    type={'solid'}
                    value={reason}
                  />
                )}
              </>
              {/* pkrMotivoRO */}
              <>
                {!(props.route?.params?.readOnly ?? false) ? null : (
                  <Picker
                    disabled={true}
                    label={'Motivo'}
                    leftIconMode={'inset'}
                    onValueChange={newPkrMotivoROValue => {
                      try {
                        setReason(newPkrMotivoROValue);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    options={reasonsList}
                    placeholder={'Motivo del cambio'}
                    type={'solid'}
                    value={reason}
                  />
                )}
              </>
              <>
                {!reasonError ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {reasonError}
                  </Text>
                )}
              </>
            </View>

            <View
              style={StyleSheet.applyWidth({ marginTop: 5 }, dimensions.width)}
            >
              {/* ccDpDateFrom */}
              <>
                {!!(props.route?.params?.readOnly ?? false) ? null : (
                  <Utils.CustomCodeErrorBoundary>
                    <locDatePicker.LocDatePicker
                      theme={theme}
                      label="Desde"
                      mode="date"
                      format="dd/mm/yyyy"
                      locale="es"
                      date={dateFrom}
                      onValueChange={setDateFrom}
                    />
                  </Utils.CustomCodeErrorBoundary>
                )}
              </>
              {/* ccDpDateFromRO */}
              <>
                {!(props.route?.params?.readOnly ?? false) ? null : (
                  <Utils.CustomCodeErrorBoundary>
                    <locDatePicker.LocDatePicker
                      disabled={true}
                      theme={theme}
                      label="Desde"
                      mode="date"
                      format="dd/mm/yyyy"
                      locale="es"
                      date={dateFrom}
                    />
                  </Utils.CustomCodeErrorBoundary>
                )}
              </>
              <>
                {!dateFromError ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {dateFromError}
                  </Text>
                )}
              </>
            </View>

            <View
              style={StyleSheet.applyWidth({ marginTop: 5 }, dimensions.width)}
            >
              {/* ccDpDateTo */}
              <>
                {!!(props.route?.params?.readOnly ?? false) ? null : (
                  <Utils.CustomCodeErrorBoundary>
                    <locDatePicker.LocDatePicker
                      theme={theme}
                      label="Hasta"
                      mode="date"
                      format="dd/mm/yyyy"
                      locale="es"
                      date={dateTo}
                      onValueChange={setDateTo}
                    />
                  </Utils.CustomCodeErrorBoundary>
                )}
              </>
              {/* ccDpDateToRO */}
              <>
                {!(props.route?.params?.readOnly ?? false) ? null : (
                  <Utils.CustomCodeErrorBoundary>
                    <locDatePicker.LocDatePicker
                      disabled={true}
                      theme={theme}
                      label="Hasta"
                      mode="date"
                      format="dd/mm/yyyy"
                      locale="es"
                      date={dateTo}
                    />
                  </Utils.CustomCodeErrorBoundary>
                )}
              </>
              <>
                {!dateToError ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {dateToError}
                  </Text>
                )}
              </>
            </View>
          </View>
        </View>

        <View
          style={StyleSheet.applyWidth(
            {
              justifyContent: 'center',
              marginBottom: 15,
              marginLeft: '3%',
              marginRight: '3%',
              marginTop: 15,
            },
            dimensions.width
          )}
        >
          {/* Button Solid */}
          <>
            {!!(props.route?.params?.readOnly ?? false) ? null : (
              <Button
                onPress={() => {
                  const handler = async () => {
                    try {
                      const validateError = validate();
                      if (validateError) {
                        return;
                      }
                      setIoAction('saving');
                      setLoading(true);
                      const fetchResult = (
                        await dcSSRequestCtuPOST.mutateAsync({
                          emp_id: Constants['my_emp_id'],
                          req_ctu_from: Date2IsoDateString(dateFrom),
                          req_ctu_rea_id: parseInt(reason, 10),
                          req_ctu_to: Date2IsoDateString(dateTo),
                          req_ctu_wor_id: parseInt(worksch, 10),
                          req_datetime: now(),
                        })
                      )?.json;
                      setLoading(false);
                      const isError = postResultToast(fetchResult);
                      if (isError) {
                        return;
                      }
                      delayNav(2000, 'back', '');
                    } catch (err) {
                      console.error(err);
                    }
                  };
                  handler();
                }}
                style={StyleSheet.applyWidth(
                  {
                    backgroundColor: theme.colors.primary,
                    borderRadius: 10,
                    color: theme.colors.background,
                    fontFamily: 'System',
                    fontWeight: '700',
                    marginLeft: '6%',
                    marginRight: '6%',
                    minHeight: 48,
                    textAlign: 'center',
                  },
                  dimensions.width
                )}
                title={'Solicitar'}
              />
            )}
          </>
        </View>
      </KeyboardAwareScrollView>
    </ScreenContainer>
  );
};

export default withTheme(SolicitudCambioDeTurnoScreen);
