import React from 'react';
import * as CustomCode from '../CustomCode';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import passEncrypt from '../global-functions/passEncrypt';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import {
  Button,
  Container,
  ScreenContainer,
  TextField,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import * as WebBrowser from 'expo-web-browser';
import { Image, Platform, Text, View } from 'react-native';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';

const IniciarSesionScreen = props => {
  const { theme, navigation } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();
  const [errorMessage, setErrorMessage] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [whoami, setWhoami] = React.useState({});
  const [email, setEmail] = React.useState('');
  const [password, setPassword] = React.useState('');
  const validate = (value, regex, target_var) => {
    const invalid = !regex.test(value);
    console.log(`Value: ${value} Valid: ${!invalid}`);

    setVariable({ key: target_var, value: invalid });

    return invalid;
  };

  const resetStackNav = () => {
    if (props.navigation.getState) {
      const navState = props.navigation.getState();

      if (navState.routes.length > 1)
        props.navigation.reset({
          index: 0,
          routes: [{ name: 'Auth' }],
        });
    }
  };

  const hideSplash = () => {
    CustomCode.hideSplash();
  };

  // Extract right error message from payload
  const extractError = body => {
    if (body.error) {
      if (body.error.details && body.error.details.length) {
        return `${body.error.details[0].path.substring(1)} ${body.error.details[0].message
          }`;
      }
      return body.error.message;
    }
    return '';
  };

  const emailCheck = email => {
    return validate(
      email,
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
      'SHOW_EMAIL_ERROR'
    );
  };

  const passwordCheck = password => {
    return false; // En Login no doy pistas del formato de la password

    // De 8 a 100 cars, debe contener  1 mayscula, 1 minscula y 2 dgitos
    return validate(
      password,
      /^(?=.*[a-z])(?=.*[A-Z])(?=(.*\d){2,})[A-Za-z\d@$!%*?&]{8,100}$/,
      'SHOW_PASSWORD_ERROR'
    );
  };

  // Checks if logged in
  const loggedIn = (authToken, refreshToken, whoami) => {
    return true;
  };

  const logVar = aVar => {
    if (variables['logging']) console.log(`log value: ${JSON.stringify(aVar)}`);
    return aVar;
  };

  const breakIfEmpty = value => {
    const empty = isEmpty(value);

    if (empty) setLoading(false);

    console.log(`breakIfEmpty(${value}): ${empty}`);

    return empty;
  };

  const isEmpty = value => {
    const empty = !value || value?.length == 0;

    // console.log(`checking emptyness of '${value}': ${empty}`);

    return empty;
    // return true;
  };

  const skipIfLogged = (whoami, refreshToken) => {
    const homeScreen = 'InicioScreen';

    // console.log(`skipIfLogged(whoami: ${JSON.stringify(whoami)}, refreshToken: ${refreshToken})`);
    console.log(
      `skipIfLogged(whoami: ${whoami}, refreshToken: ${refreshToken})`
    );
    console.log(whoami);

    if (whoami?.error) {
      if (isEmpty(refreshToken)) {
        console.log(
          'whoami error. token expir y no hay refresh token. Obligando a login'
        );
        setLoading(false);
        return true;
      } else {
        console.log(
          'whoami error. token expir, las acciones subsiguientes trataran de renovarlo'
        );
        return false;
      }
    }
    // if(whoami?.emp_doc_number && refreshToken?.length) {  No tiene sentido preguntar, si no retorn error estoy conectado
    else console.log('whoami ok. Estoy conectado');

    setLoading(false);
    console.log(`skipIfLogged: navigating to ${homeScreen}`);
    props.navigation.navigate('Main', { screen: homeScreen });
    return true;
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();
  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        hideSplash();
        setLoading(true);
        if (breakIfEmpty(Constants['AUTH_HEADER'])) {
          return;
        }
        const a_whoami = (
          await DcSSApi.whoamiGET(Constants, {
            comid: Constants['my_emp_com_id'],
          })
        )?.json;
        const skip = skipIfLogged(a_whoami, Constants['REFRESH_TOKEN']);
        if (skip) {
          return;
        }
        const a_refresh = (
          await DcSSApi.tokenRefreshPOST(Constants, {
            refreshToken: Constants['REFRESH_TOKEN'],
          })
        )?.json;
        setLoading(false);
        const a_refresh_error = a_refresh?.error;
        if (a_refresh_error) {
          return;
        }
        const a_accessToken = a_refresh?.accessToken;
        const newAUTH_HEADER = setGlobalVariableValue({
          key: 'AUTH_HEADER',
          value: 'Bearer ' + a_accessToken,
        });
        console.log(newAUTH_HEADER);
        navigation.navigate('Main', { screen: 'InicioScreen' });
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer
      hasSafeArea={true}
      scrollable={false}
      style={StyleSheet.applyWidth(
        {
          backgroundColor: theme.colors.background,
          height: '100%',
          justifyContent: 'space-between',
        },
        dimensions.width
      )}
    >
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={'connecting'}
      />

      <KeyboardAwareScrollView
        enableAutomaticScroll={true}
        enableOnAndroid={false}
        enableResetScrollToCoords={true}
        extraHeight={200}
      >
        <View
          style={StyleSheet.applyWidth(
            {
              alignItems: 'center',
              alignSelf: 'center',
              paddingTop: '5%',
              width: '90%',
            },
            dimensions.width
          )}
        >
          {/* app-logo */}
          <Image
            resizeMode={'contain'}
            source={Images.DigiSSTransp}
            style={StyleSheet.applyWidth(
              { height: 100, marginTop: 20, overflow: 'hidden', width: 300 },
              dimensions.width
            )}
          />
          <View
            style={StyleSheet.applyWidth(
              { alignContent: 'center', alignItems: 'center', marginTop: 20 },
              dimensions.width
            )}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.strong,
                  fontFamily: 'System',
                  fontSize: 28,
                  fontWeight: '700',
                  marginBottom: 15,
                  opacity: 0.8,
                  textAlign: 'center',
                  textDecorationColor: theme.colors.primary,
                  textTransform: 'none',
                },
                dimensions.width
              )}
            >
              {'Bienvenido !'}
            </Text>
          </View>
        </View>

        <View
          style={StyleSheet.applyWidth(
            { justifyContent: 'center', minHeight: 50 },
            dimensions.width
          )}
        >
          <View
            style={StyleSheet.applyWidth(
              { alignContent: 'center', alignItems: 'center' },
              dimensions.width
            )}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.strong,
                  typography: theme.typography.subtitle2,
                },
                dimensions.width
              )}
            >
              {'Ingres tu mail y contrasea'}
            </Text>
          </View>
        </View>

        <View>
          <Container
            backgroundImageResizeMode={'cover'}
            style={StyleSheet.applyWidth(
              { alignSelf: 'flex-start' },
              dimensions.width
            )}
          >
            <View
              style={StyleSheet.applyWidth(
                {
                  alignContent: 'center',
                  justifyContent: 'flex-end',
                  paddingLeft: '8%',
                  paddingRight: '8%',
                },
                dimensions.width
              )}
            >
              {/* Email */}
              <TextField
                autoCapitalize={'none'}
                keyboardType={'email-address'}
                label={'Email'}
                onChangeText={newEmailValue => {
                  const email = newEmailValue;
                  try {
                    setEmail(email);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                returnKeyType={'done'}
                type={'solid'}
                value={email}
              />
              <>
                {!Constants['SHOW_EMAIL_ERROR'] ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {'Debe ser una direccin de email vlida'}
                  </Text>
                )}
              </>
              {/* Password */}
              <TextField
                label={'Contrasea'}
                onChangeText={newPasswordValue => {
                  const password = newPasswordValue;
                  try {
                    setPassword(password);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                placeholder={''}
                returnKeyType={'done'}
                secureTextEntry={true}
                style={StyleSheet.applyWidth(
                  { marginTop: '5%' },
                  dimensions.width
                )}
                type={'solid'}
                value={password}
              />
              <>
                {!Constants['SHOW_PASSWORD_ERROR'] ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {'De 8 a 100 cars,  1 mayscula, 1 minscula, 2 dgitos'}
                  </Text>
                )}
              </>
            </View>

            <View
              style={StyleSheet.applyWidth(
                {
                  alignItems: 'center',
                  marginBottom: '2%',
                  marginLeft: '8%',
                  marginRight: '8%',
                  marginTop: '5%',
                  paddingBottom: 4,
                  paddingTop: 4,
                },
                dimensions.width
              )}
            >
              <>
                {isEmpty(errorMessage) ? null : (
                  <Text
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 12,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {errorMessage}
                  </Text>
                )}
              </>
            </View>
          </Container>
        </View>

        <View
          style={StyleSheet.applyWidth(
            {
              alignItems: 'center',
              marginBottom: '2%',
              marginTop: '2%',
              paddingLeft: '6%',
              paddingRight: '6%',
            },
            dimensions.width
          )}
        >
          <Touchable
            onPress={() => {
              try {
                navigation.navigate('Auth', { screen: 'NuevoUsuarioScreen' });
              } catch (err) {
                console.error(err);
              }
            }}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.secondary,
                  marginBottom: '3%',
                  marginTop: '2%',
                  textDecorationLine: 'underline',
                },
                dimensions.width
              )}
            >
              {'No tens cuenta ?  Cre una !'}
            </Text>
          </Touchable>
          {/* Button Solid */}
          <Button
            disabled={false}
            loading={false}
            onPress={() => {
              const handler = async () => {
                try {
                  loggedIn(undefined, undefined, undefined);
                  undefined;
                  if (emailCheck(email)) {
                    return;
                  }
                  if (passwordCheck(password)) {
                    return;
                  }
                  const loginResponse = (
                    await DcSSApi.loginPOST(Constants, {
                      email: email?.toLowerCase(),
                      password: passEncrypt(password),
                    })
                  )?.json;
                  console.log(loginResponse);
                  const errorMessage = extractError(loginResponse);
                  setErrorMessage(errorMessage);
                  if (errorMessage?.length) {
                    return;
                  }
                  const authToken = loginResponse?.accessToken;
                  const AUTH_HEADER = setGlobalVariableValue({
                    key: 'AUTH_HEADER',
                    value: 'Bearer ' + authToken,
                  });
                  console.log(AUTH_HEADER);
                  if (!authToken) {
                    return;
                  }
                  const a_emp_id = loginResponse?.emp_id;
                  const a_emp_com_id = loginResponse?.emp_com_id;
                  setGlobalVariableValue({
                    key: 'my_emp_id',
                    value: parseInt(a_emp_id, 10),
                  });
                  setGlobalVariableValue({
                    key: 'my_emp_com_id',
                    value: a_emp_com_id,
                  });
                  const refreshToken = loginResponse?.refreshToken;
                  const REFRESH_TOKEN = setGlobalVariableValue({
                    key: 'REFRESH_TOKEN',
                    value: refreshToken,
                  });
                  console.log(REFRESH_TOKEN);
                  navigation.navigate('Permissions', {
                    screen: 'BienvenidoScreen',
                  });
                } catch (err) {
                  console.error(err);
                }
              };
              handler();
            }}
            style={StyleSheet.applyWidth(
              {
                backgroundColor: theme.colors.primary,
                borderRadius: 10,
                color: theme.colors.background,
                fontFamily: 'System',
                fontWeight: '700',
                marginLeft: '6%',
                marginRight: '6%',
                marginTop: '2%',
                minHeight: 48,
                textAlign: 'center',
                width: '100%',
              },
              dimensions.width
            )}
            title={'Iniciar sesin'}
          />
        </View>
        <>
          {!(Platform.OS === 'web') ? null : (
            <View
              style={StyleSheet.applyWidth(
                {
                  alignItems: 'center',
                  flexDirection: 'row',
                  justifyContent: 'space-around',
                  marginTop: '2%',
                  paddingLeft: '6%',
                  paddingRight: '6%',
                },
                dimensions.width
              )}
            >
              <View
                style={StyleSheet.applyWidth(
                  { alignItems: 'center' },
                  dimensions.width
                )}
              >
                <Touchable
                  onPress={() => {
                    const handler = async () => {
                      try {
                        await WebBrowser.openBrowserAsync(
                          'https://play.google.com/store/apps/details?id=com.digicard.dss'
                        );
                      } catch (err) {
                        console.error(err);
                      }
                    };
                    handler();
                  }}
                >
                  {/* imgGoogle */}
                  <Image
                    resizeMode={'contain'}
                    source={Images.DisponibleEnGooglePlay}
                    style={StyleSheet.applyWidth(
                      { height: 40, width: 120 },
                      dimensions.width
                    )}
                  />
                </Touchable>
                {/* qrGoogle */}
                <Image
                  resizeMode={'cover'}
                  source={Images.QrGoogle}
                  style={StyleSheet.applyWidth(
                    { height: 90, marginTop: 5, width: 90 },
                    dimensions.width
                  )}
                />
              </View>

              <View
                style={StyleSheet.applyWidth(
                  { alignItems: 'center' },
                  dimensions.width
                )}
              >
                <Touchable
                  onPress={() => {
                    const handler = async () => {
                      try {
                        await WebBrowser.openBrowserAsync(
                          'https://apps.apple.com/us/app/digicard-self-service/id1621603154'
                        );
                      } catch (err) {
                        console.error(err);
                      }
                    };
                    handler();
                  }}
                >
                  {/* imgApple */}
                  <Image
                    resizeMode={'contain'}
                    source={Images.DisponibleEnApple}
                    style={StyleSheet.applyWidth(
                      { height: 40, width: 120 },
                      dimensions.width
                    )}
                  />
                </Touchable>
                {/* qrApple */}
                <Image
                  resizeMode={'cover'}
                  source={Images.QrApple}
                  style={StyleSheet.applyWidth(
                    { height: 90, marginTop: 5, width: 90 },
                    dimensions.width
                  )}
                />
              </View>
            </View>
          )}
        </>
      </KeyboardAwareScrollView>
    </ScreenContainer>
  );
};

export default withTheme(IniciarSesionScreen);
