import React from 'react';
import { DatePicker } from '@draftbit/ui';

export const LocDatePicker = props => {
  // console.log(`LocDatePicker props: ${JSON.stringify(props, undefined, 2)}`);

  return (
    <DatePicker
      {...props}
      onDateChange={newDatePickerValue => {
        try {
          props?.onValueChange(newDatePickerValue);
        } catch (err) {
          console.error(err);
        }
      }}
      style={{ borderColor: props.theme.colors['Light'] }}
      leftIconMode={props.leftIconMode || 'inset'}
      type={'solid'}
    />
  );
};
