import React from 'react';
import * as CustomCode from '../CustomCode';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import {
  Icon,
  ScreenContainer,
  Surface,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import {
  ActivityIndicator,
  FlatList,
  ScrollView,
  Text,
  View,
} from 'react-native';
import { Fetch } from 'react-request';

const NotificacionesScreen = props => {
  const { theme } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const [daySheet, setDaySheet] = React.useState([]);
  const [requestsType, setRequestsType] = React.useState('E');
  const [screenName, setScreenName] = React.useState('Notificaciones');
  const [showing, setShowing] = React.useState(0);
  const IsoString2ShortDate = utcString => {
    return CustomCode.IsoString2ShortDate(utcString);
  };

  const reqStatus2String = reqStatus => {
    const reqStatuses = {
      E: 'En proceso',
      A: 'Aprobada',
      D: 'Denegada',
      T: 'Todas',
    };

    return reqStatuses[reqStatus];
  };

  const log = value => {
    return CustomCode.log(value);
  };

  const setShow = what => {
    setShowing(what);

    calcFilter(what);

    // console.log(`setShow(${what})`);
    CustomCode.log(`setShow(${what})`);
  };

  const friendlyDate = aDate => {
    return CustomCode.Date2String(
      CustomCode.IsoDateStringToDate(aDate),
      'DD/MM HH:mm'
    );
  };

  const calcFilter = showOption => {
    // console.log(`calcFilter(${showOption})`);
    CustomCode.log(`calcFilter(${showOption})`);
    switch (showOption) {
      case 0:
        setRequestsType('N');
        break;

      case 1:
        setRequestsType('R');
        break;

      case 2:
        setRequestsType('A');
        break;
    }
  };

  const reqTypeToString = reqType => {
    const reqTypes = {
      vac: 'Vacaciones',
      ctu: 'Cambio de turno',
      pya: 'Permisos y ausencias',
      rys: 'Retiros y salidas intermedias',
      epp: 'EPP',
    };

    return reqTypes[reqType];
  };

  // Convierte una fecha en formato YYYY-MM-DD a Date
  const IsoDateStringToUTCDate = sDate => {
    return CustomCode.IsoDateStringToUTCDate(aDate);
  };

  const now = () => {
    return new Date().toISOString();
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]
  const dcSSNotificationPATCH = DcSSApi.useNotificationPATCH();

  return (
    <ScreenContainer
      hasSafeArea={true}
      scrollable={false}
      style={StyleSheet.applyWidth({ marginBottom: 15 }, dimensions.width)}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      {/* DatePickerHeaderView */}
      <View
        style={StyleSheet.applyWidth(
          { marginBottom: 10, marginLeft: 15, marginRight: 15, marginTop: 20 },
          dimensions.width
        )}
      >
        <CustomCode.rnSegmentedControlTab
          values={['Nuevas', 'Leidas', 'Todas']}
          onTabPress={index => setShow(index)}
          selectedIndex={showing}
        />
      </View>

      <ScrollView
        bounces={true}
        style={StyleSheet.applyWidth({ height: 100 }, dimensions.width)}
      >
        <DcSSApi.FetchNotificationsByEmpGET
          emp_id={Constants['my_emp_id']}
          limit={100}
          status={requestsType}
        >
          {({ loading, error, data, refetchNotificationsByEmp }) => {
            const fetchData = data?.json;
            if (loading) {
              return <ActivityIndicator />;
            }

            if (error || data?.status < 200 || data?.status >= 300) {
              return <ActivityIndicator />;
            }

            return (
              <>
                {/* NotificacionesSectionView */}
                <View
                  style={StyleSheet.applyWidth(
                    { marginLeft: '3%', marginRight: '3%' },
                    dimensions.width
                  )}
                >
                  {/* ItemsList */}
                  <FlatList
                    data={fetchData}
                    keyExtractor={(itemsListData, index) =>
                      itemsListData?.id ??
                      itemsListData?.uuid ??
                      index.toString()
                    }
                    listKey={'1vBv0LIP'}
                    numColumns={1}
                    renderItem={({ item, index }) => {
                      const itemsListData = item;
                      return (
                        <Touchable
                          onPress={() => {
                            const handler = async () => {
                              try {
                                if (itemsListData?.not_readed) {
                                  return;
                                }
                                console.log('No leida');
                                (
                                  await dcSSNotificationPATCH.mutateAsync({
                                    not_id: itemsListData?.not_id,
                                    read: true,
                                    read_date: now(),
                                  })
                                )?.json;
                                await refetchNotificationsByEmp();
                              } catch (err) {
                                console.error(err);
                              }
                            };
                            handler();
                          }}
                          style={StyleSheet.applyWidth(
                            { marginBottom: 5, marginTop: 5 },
                            dimensions.width
                          )}
                        >
                          <Surface
                            elevation={3}
                            style={StyleSheet.applyWidth(
                              {
                                backgroundColor: theme.colors.verylight,
                                borderRadius: 8,
                                marginRight: 3,
                                minHeight: 40,
                                paddingBottom: 3,
                                paddingLeft: 8,
                                paddingRight: 8,
                                paddingTop: 3,
                              },
                              dimensions.width
                            )}
                          >
                            {/* TopView */}
                            <View>
                              <View
                                style={StyleSheet.applyWidth(
                                  {
                                    flexDirection: 'row',
                                    justifyContent: 'space-between',
                                  },
                                  dimensions.width
                                )}
                              >
                                {/* txtSubject */}
                                <Text
                                  style={StyleSheet.applyWidth(
                                    {
                                      alignSelf: 'flex-start',
                                      color: theme.colors.strong,
                                      fontFamily: 'System',
                                      fontWeight: '600',
                                      marginBottom: 3,
                                    },
                                    dimensions.width
                                  )}
                                >
                                  {itemsListData?.not_subject}
                                </Text>

                                <View
                                  style={StyleSheet.applyWidth(
                                    {
                                      alignItems: 'flex-end',
                                      alignSelf: 'flex-end',
                                    },
                                    dimensions.width
                                  )}
                                >
                                  {/* icnStatus */}
                                  <>
                                    {!itemsListData?.not_read ? null : (
                                      <Icon
                                        color={theme.colors.right}
                                        name={'AntDesign/check'}
                                        size={18}
                                        style={StyleSheet.applyWidth(
                                          { marginRight: 5 },
                                          dimensions.width
                                        )}
                                      />
                                    )}
                                  </>
                                </View>
                              </View>

                              <Text
                                ellipsizeMode={'clip'}
                                numberOfLines={1}
                                style={StyleSheet.applyWidth(
                                  { color: theme.colors.strong, fontSize: 12 },
                                  dimensions.width
                                )}
                              >
                                {itemsListData?.not_body}
                              </Text>
                            </View>
                            {/* BottomView */}
                            <View
                              style={StyleSheet.applyWidth(
                                {
                                  flexDirection: 'row',
                                  justifyContent: 'space-between',
                                  marginTop: 5,
                                },
                                dimensions.width
                              )}
                            >
                              {/* txtFrom */}
                              <Text
                                style={StyleSheet.applyWidth(
                                  {
                                    alignSelf: 'flex-start',
                                    color: theme.colors.strong,
                                    fontSize: 10,
                                  },
                                  dimensions.width
                                )}
                              >
                                {itemsListData?.not_from_emp_name}
                              </Text>
                              {/* txtDate */}
                              <>
                                {!itemsListData?.not_date ? null : (
                                  <Text
                                    style={StyleSheet.applyWidth(
                                      {
                                        alignSelf: 'flex-end',
                                        color: theme.colors.strong,
                                        fontSize: 10,
                                      },
                                      dimensions.width
                                    )}
                                  >
                                    {friendlyDate(itemsListData?.not_date)}
                                  </Text>
                                )}
                              </>
                            </View>
                          </Surface>
                        </Touchable>
                      );
                    }}
                  />
                </View>
              </>
            );
          }}
        </DcSSApi.FetchNotificationsByEmpGET>
      </ScrollView>
    </ScreenContainer>
  );
};

export default withTheme(NotificacionesScreen);
