import * as React from 'react';
import {
  useQuery,
  useMutation,
  useIsFetching,
  useQueryClient,
} from 'react-query';
import useFetch from 'react-fetch-hook';
import { useIsFocused } from '@react-navigation/native';
import { handleResponse, isOkStatus } from '../utils/handleRestApiResponse';
import usePrevious from '../utils/usePrevious';
import encodeQueryParam from '../utils/encodeQueryParam';
import * as GlobalVariables from '../config/GlobalVariableContext';

export const companyConfigsGET = (Constants, { comId }, handlers = {}) =>
  fetch(
    `https://dcss-api.herokuapp.com/config/${
      typeof comId === 'string' ? comId : JSON.stringify(comId ?? '')
    }`,
    {
      headers: {
        Accept: 'application/json',
        Authorization: Constants['AUTH_HEADER'],
        'Content-Type': 'application/json',
      },
    }
  ).then(res => handleResponse(res, handlers));

export const useCompanyConfigsGET = (
  args = {},
  { refetchInterval, handlers = {} } = {}
) => {
  const Constants = GlobalVariables.useValues();
  return useQuery(
    ['CompanyConfigs', args],
    () => companyConfigsGET(Constants, args, handlers),
    {
      refetchInterval,
    }
  );
};

export const FetchCompanyConfigsGET = ({
  children,
  onData = () => {},
  handlers = {},
  refetchInterval,
  comId,
}) => {
  const Constants = GlobalVariables.useValues();
  const isFocused = useIsFocused();
  const prevIsFocused = usePrevious(isFocused);

  const {
    isLoading: loading,
    data,
    error,
    refetch,
  } = useCompanyConfigsGET(
    { comId },
    { refetchInterval, handlers: { onData, ...handlers } }
  );

  React.useEffect(() => {
    if (!prevIsFocused && isFocused) {
      refetch();
    }
  }, [isFocused, prevIsFocused]);

  React.useEffect(() => {
    if (error) {
      console.error('Fetch error: ' + error.status + ' ' + error.statusText);
      console.error(error);
    }
  }, [error]);
  return children({ loading, data, error, refetchCompanyConfigs: refetch });
};

export const requestDeleteDELETE = (Constants, { reqid }, handlers = {}) =>
  fetch(
    `https://dcss-api.herokuapp.com/requests/${
      typeof reqid === 'string' ? reqid : JSON.stringify(reqid ?? '')
    }`,
    {
      headers: {
        Accept: 'application/json',
        Authorization: Constants['AUTH_HEADER'],
        'Content-Type': 'application/json',
      },
      method: 'DELETE',
    }
  ).then(res => handleResponse(res, handlers));

export const useRequestDeleteDELETE = (
  initialArgs = {},
  { handlers = {} } = {}
) => {
  const queryClient = useQueryClient();
  const Constants = GlobalVariables.useValues();
  return useMutation(
    args =>
      requestDeleteDELETE(Constants, { ...initialArgs, ...args }, handlers),
    {
      onError: (err, variables, { previousValue }) => {
        if (previousValue) {
          return queryClient.setQueryData('requests', previousValue);
        }
      },
      onSettled: () => {
        queryClient.invalidateQueries('request');
        queryClient.invalidateQueries('requests');
      },
    }
  );
};

