export const employeePATCH = (
  Constants,
  {
    address,
    birth_date,
    contact,
    emp_id,
    emp_photo,
    first_name,
    health,
    last_name,
    mail,
    phone,
    transport,
  },
  handlers = {}
) =>
  fetch(
    `https://dcss-api.herokuapp.com/employees/${
      typeof emp_id === 'string' ? emp_id : JSON.stringify(emp_id ?? '')
    }`,
    {
      body: JSON.stringify({
        emp_first_name: first_name,
        emp_last_name: last_name,
        emp_birth_date: birth_date,
        emp_mail: mail,
        emp_phone: phone,
        emp_address: address,
        emp_contact: contact,
        emp_health: health,
        emp_transport: transport,
        emp_photo: emp_photo,
      }),
      headers: {
        Accept: 'application/json',
        Authorization: Constants['AUTH_HEADER'],
        'Content-Type': 'application/json',
      },
      method: 'PATCH',
    }
  ).then(res => handleResponse(res, handlers));

export const useEmployeePATCH = (initialArgs = {}, { handlers = {} } = {}) => {
  const queryClient = useQueryClient();
  const Constants = GlobalVariables.useValues();
  return useMutation(
    args => employeePATCH(Constants, { ...initialArgs, ...args }, handlers),
    {
      onError: (err, variables, { previousValue }) => {
        if (previousValue) {
          return queryClient.setQueryData('employee', previousValue);
        }
      },
      onSettled: () => {
        queryClient.invalidateQueries('employee');
        queryClient.invalidateQueries('employees');
      },
    }
  );
};

