import React from 'react';
import * as CustomCode from '../CustomCode';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import { Button, ScreenContainer, Touchable, withTheme } from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, Text, View } from 'react-native';

const PermisoDeCamaraScreen = props => {
  const { theme, navigation } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();
  const getPermission = async () => {
    if (CustomCode.Platform.OS === 'web') return true;

    const { status } = await CustomCode.Camera.getCameraPermissionsAsync();

    console.log(`status: ${status}`);

    return status === 'granted';
  };

  const askPermission = async () => {
    const { status } = await CustomCode.Camera.requestCameraPermissionsAsync();

    console.log(`status: ${status}`);

    return status === 'granted';
    // props.navigation.navigate(target);
  };
  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        const hasPermission = await getPermission();
        if (!hasPermission) {
          return;
        }
        navigation.navigate('Permissions', {
          screen: 'CompletaInicioSessionScreen',
        });
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer
      hasSafeArea={true}
      scrollable={false}
      style={StyleSheet.applyWidth(
        {
          backgroundColor: theme.colors.background,
          justifyContent: 'space-between',
        },
        dimensions.width
      )}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader {...props} left={{}} />
      </Utils.CustomCodeErrorBoundary>
      <View
        style={StyleSheet.applyWidth(
          {
            alignContent: 'center',
            alignItems: 'center',
            alignSelf: 'center',
            justifyContent: 'center',
          },
          dimensions.width
        )}
      >
        <Image
          resizeMode={'contain'}
          source={Images.PermissionsCamera}
          style={StyleSheet.applyWidth(
            { height: 150, marginBottom: 16, overflow: 'hidden', width: 150 },
            dimensions.width
          )}
        />
        <Text
          allowFontScaling={true}
          ellipsizeMode={'tail'}
          style={StyleSheet.applyWidth(
            {
              alignSelf: 'center',
              color: theme.colors.strong,
              fontFamily: 'System',
              fontSize: 28,
              fontWeight: '700',
            },
            dimensions.width
          )}
          textBreakStrategy={'highQuality'}
        >
          {'Permitir Cmara'}
        </Text>

        <Text
          allowFontScaling={true}
          ellipsizeMode={'tail'}
          style={StyleSheet.applyWidth(
            {
              alignSelf: 'center',
              color: theme.colors.strong,
              fontSize: 16,
              lineHeight: 28,
              marginLeft: '3%',
              marginRight: '3%',
              textAlign: 'center',
            },
            dimensions.width
          )}
          textBreakStrategy={'highQuality'}
        >
          {
            'Lo necesitaremos para tomar fotos, acceder al carrete de la cmara o grabar videos.\n'
          }
        </Text>
      </View>

      <View
        style={StyleSheet.applyWidth(
          {
            alignContent: 'center',
            alignItems: 'center',
            justifyContent: 'flex-end',
            marginBottom: '10%',
            marginLeft: '6%',
            marginRight: '6%',
          },
          dimensions.width
        )}
      >
        <Touchable
          onPress={() => {
            try {
              navigation.navigate('Permissions', {
                screen: 'CompletaInicioSessionScreen',
              });
            } catch (err) {
              console.error(err);
            }
          }}
          style={StyleSheet.applyWidth(
            { marginBottom: '8%' },
            dimensions.width
          )}
        >
          <Text
            allowFontScaling={true}
            ellipsizeMode={'tail'}
            style={StyleSheet.applyWidth(
              {
                color: theme.colors.strong,
                opacity: 0.52,
                textAlign: 'center',
                textTransform: 'uppercase',
                typography: theme.typography.button,
              },
              dimensions.width
            )}
            textBreakStrategy={'highQuality'}
          >
            {'Saltar'}
          </Text>
        </Touchable>
        {/* Button Solid */}
        <Button
          onPress={() => {
            const handler = async () => {
              try {
                setGlobalVariableValue({
                  key: 'perms_got',
                  value: 1,
                });
                const granted = await askPermission();
                if (!granted) {
                  return;
                }
                navigation.navigate('Permissions', {
                  screen: 'CompletaInicioSessionScreen',
                });
              } catch (err) {
                console.error(err);
              }
            };
            handler();
          }}
          style={StyleSheet.applyWidth(
            {
              backgroundColor: theme.colors.primary,
              borderRadius: 10,
              color: theme.colors.background,
              fontFamily: 'System',
              fontWeight: '700',
              marginLeft: '6%',
              marginRight: '6%',
              minHeight: 48,
              textAlign: 'center',
              width: '100%',
            },
            dimensions.width
          )}
          title={'Permitir Cmara'}
        />
      </View>
    </ScreenContainer>
  );
};

export default withTheme(PermisoDeCamaraScreen);
