import React from 'react';
import * as CustomCode from '../CustomCode';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import { Button, Container, ScreenContainer, withTheme } from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { ActivityIndicator, Text, View } from 'react-native';
import { Fetch } from 'react-request';

const BienvenidoScreen = props => {
  const { theme, navigation } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const [screenName, setScreenName] = React.useState(undefined);
  const stopIfNonZero = value => {
    // const variables = CustomCode.useValues();
    const perms_got = Variables['perms_got']; //

    console.log(`value: ${value}, perms_got: ${perms_got}`);

    return value == 0 ? 1 : 0;
  };

  const HasAllPermissions = async () => {
    if (CustomCode.Platform.OS === 'web') return true;

    // Camera
    const { status } = await CustomCode.Camera.getCameraPermissionsAsync();

    console.log(`Camera Permission status: ${status}`);

    if (status !== 'granted') return false;

    // Notifications
    const settings = await CustomCode.ExpoNotifications.getPermissionsAsync();
    console.log(
      `Notifications Permission status: ${settings.status}, ios: ${settings?.ios}`
    );

    return (
      settings.granted ||
      settings.ios?.status ===
        CustomCode.ExpoNotifications.IosAuthorizationStatus.AUTHORIZED
    );
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();
  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        if (false) {
          return;
        }
        console.log(Constants['my_emp_id']);
        const hasPermissions = await HasAllPermissions();
        if (!hasPermissions) {
          return;
        }
        console.log('Not stopped');
        navigation.navigate('Permissions', {
          screen: 'CompletaInicioSessionScreen',
        });
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer
      hasSafeArea={true}
      scrollable={false}
      style={StyleSheet.applyWidth(
        { justifyContent: 'space-between' },
        dimensions.width
      )}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          left={{}}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <View
        style={StyleSheet.applyWidth(
          {
            alignItems: 'center',
            alignSelf: 'center',
            justifyContent: 'center',
          },
          dimensions.width
        )}
      >
        <View
          style={StyleSheet.applyWidth(
            { alignItems: 'center', marginTop: 10 },
            dimensions.width
          )}
        >
          <DcSSApi.FetchEmployeeConfigGET emp_id={Constants['my_emp_id']}>
            {({ loading, error, data, refetchEmployeeConfig }) => {
              const fetchData = data?.json;
              if (loading) {
                return <ActivityIndicator />;
              }

              if (error || data?.status < 200 || data?.status >= 300) {
                return <ActivityIndicator />;
              }

              return (
                <>
                  <View
                    style={StyleSheet.applyWidth(
                      {
                        alignSelf: 'stretch',
                        marginBottom: 10,
                        marginLeft: 5,
                        marginRight: 5,
                        marginTop: 10,
                      },
                      dimensions.width
                    )}
                  >
                    <Text
                      style={StyleSheet.applyWidth(
                        {
                          color: theme.colors.secondary,
                          fontFamily: 'Montserrat_600SemiBold',
                        },
                        dimensions.width
                      )}
                    >
                      {fetchData?.companyName}
                    </Text>
                  </View>

                  <Container
                    elevation={0}
                    style={StyleSheet.applyWidth(
                      {
                        alignItems: 'center',
                        borderColor: theme.colors.medium,
                        marginBottom: '5%',
                        marginTop: '5%',
                      },
                      dimensions.width
                    )}
                    useThemeGutterPadding={true}
                  >
                    <Text
                      style={StyleSheet.applyWidth(
                        {
                          alignSelf: 'center',
                          color: theme.colors.strong,
                          fontFamily: 'System',
                          fontSize: 33,
                          fontWeight: '700',
                          opacity: 1,
                          textAlign: 'center',
                          textDecorationColor: theme.colors.custom_rgb255_0_0,
                        },
                        dimensions.width
                      )}
                    >
                      {'Bienvenido '}
                      {fetchData?.emp_first_name}
                      {'!\n'}
                    </Text>
                  </Container>
                </>
              );
            }}
          </DcSSApi.FetchEmployeeConfigGET>
          <Text
            style={StyleSheet.applyWidth(
              {
                color: theme.colors.strong,
                fontFamily: 'Montserrat_500Medium',
                fontSize: 16,
                lineHeight: 24,
                marginBottom: '5%',
                marginLeft: '5%',
                marginRight: '5%',
                marginTop: '5%',
                opacity: 1,
                textAlign: 'center',
              },
              dimensions.width
            )}
          >
            {
              'A continuacin se le solicitarn ciertos permisos a otorgar para el funcionamiento correcto de la aplicacin'
            }
          </Text>

          <View
            style={StyleSheet.applyWidth(
              { justifyContent: 'center', marginBottom: 10 },
              dimensions.width
            )}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.strong,
                  fontFamily: 'System',
                  fontSize: 24,
                  fontWeight: '700',
                  marginLeft: '5%',
                  marginRight: '5%',
                  marginTop: '20%',
                  opacity: 1,
                  textAlign: 'center',
                  textDecorationColor: theme.colors.custom_rgb255_0_0,
                },
                dimensions.width
              )}
            >
              {'Seleccione iniciar para continuar\n'}
            </Text>
          </View>
        </View>
      </View>

      <View
        style={StyleSheet.applyWidth(
          {
            alignItems: 'center',
            justifyContent: 'flex-end',
            marginBottom: '10%',
            marginLeft: '6%',
            marginRight: '6%',
          },
          dimensions.width
        )}
      >
        {/* Button Solid */}
        <Button
          onPress={() => {
            try {
              console.log(Constants['perms_got']);
              navigation.navigate('Permissions', {
                screen: 'PermisoDeNotificacionesScreen',
              });
            } catch (err) {
              console.error(err);
            }
          }}
          style={StyleSheet.applyWidth(
            {
              backgroundColor: theme.colors.primary,
              borderRadius: 10,
              color: theme.colors.background,
              fontFamily: 'System',
              fontWeight: '700',
              marginLeft: '6%',
              marginRight: '6%',
              minHeight: 48,
              textAlign: 'center',
              width: '100%',
            },
            dimensions.width
          )}
          title={'Iniciar'}
        />
      </View>
    </ScreenContainer>
  );
};

export default withTheme(BienvenidoScreen);
