import React from 'react';
import * as CustomCode from '../CustomCode';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import passEncrypt from '../global-functions/passEncrypt';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import {
  Button,
  Container,
  IconButton,
  ScreenContainer,
  TextField,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, Text, View } from 'react-native';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';

const NuevoUsuarioScreen = props => {
  const { theme, navigation } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();
  const [SHOW_CUIL_ERROR, setSHOW_CUIL_ERROR] = React.useState(false);
  const [SHOW_EMAIL_ERROR, setSHOW_EMAIL_ERROR] = React.useState(false);
  const [SHOW_PASSWORD_ERROR, setSHOW_PASSWORD_ERROR] = React.useState(false);
  const [SHOW_PASSWORD_MISMATCH_ERROR, setSHOW_PASSWORD_MISMATCH_ERROR] =
    React.useState(false);
  const [cuil, setCuil] = React.useState('');
  const [email, setEmail] = React.useState('');
  const [errorMessage, setErrorMessage] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [password, setPassword] = React.useState('');
  const [password_copy, setPassword_copy] = React.useState('');
  const [showPasswordHint, setShowPasswordHint] = React.useState(false);
  const cuilCheck = cuil => {
    const chkCuitChecksum = sCUIT => {
      var aMult = '5432765432';
      var aMult = aMult.split('');
      var rawCUIT = sCUIT.replace(/-/g, '');

      // console.log('CUIT: ', sCUIT);
      // console.log('CUIT: ', rawCUIT);

      if (rawCUIT && rawCUIT.length == 11) {
        var aCUIT = rawCUIT.split('');
        var iResult = 0;
        for (var i = 0; i <= 9; i++) {
          iResult += aCUIT[i] * aMult[i];
        }
        iResult = iResult % 11;
        iResult = 11 - iResult;

        if (iResult == 11) iResult = 0;
        if (iResult == 10) iResult = 9;

        console.log(`vdigit: ${iResult}`);

        if (iResult == aCUIT[10]) return true;
      }
      return false;
    };

    const regex = /^(20|23|24|27|30|33|34)([0-9]{9}|-[0-9]{8}-[0-9]{1})$/;
    const invalid = !regex.test(cuil) || !chkCuitChecksum(cuil);
    console.log(`CUIL Value: ${cuil} Valid: ${!invalid}`);

    // setGlobalVariable({ key: 'SHOW_CUIL_ERROR', value: invalid });
    setSHOW_CUIL_ERROR(invalid);

    return invalid;
  };

  const extractError = body => {
    if (body.error) {
      if (body.error.details && body.error.details.length) {
        return `${body.error.details[0].path.substring(1)} ${
          body.error.details[0].message
        }`;
      }
      return body.error.message;
    }
    return '';
  };

  const validate = (value, regex, target_var) => {
    const invalid = !regex.test(value);
    console.log(`Value: ${value} Valid: ${!invalid}`);

    // setGlobalVariable({ key: target_var, value: invalid });
    target_var(invalid);

    return invalid;
  };

  const emailCheck = email => {
    return validate(
      email,
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
      setSHOW_EMAIL_ERROR
    );
  };

  const passwordCheck = password => {
    // De 8 a 100 cars, debe contener  1 mayscula, 1 minscula y 2 dgitos
    return validate(
      password,
      /^(?=.*[a-z])(?=.*[A-Z])(?=(.*\d){2,})[A-Za-z\d@$!%*?&]{8,100}$/,
      setSHOW_PASSWORD_ERROR
    );
  };

  const passwordCopyCheck = (passwordCopy, password) => {
    const invalid = password !== passwordCopy;

    console.log(
      `PasswordMismatch: ${invalid}, password: [${password}[], passwordCopy: [${passwordCopy}]`
    );
    // setGlobalVariable({ key: 'SHOW_PASSWORD_MISMATCH_ERROR', value: invalid });
    setSHOW_PASSWORD_MISMATCH_ERROR(invalid);

    return invalid;
  };
  // ======================== Inline Code ========================
  // to use a global variable inside the component logic
  const globalVariables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setGlobalVariable = CustomCode.useSetValue();
  // =================== End  Inline Code ========================
  const isFocused = useIsFocused();
  React.useEffect(() => {
    try {
      if (!isFocused) {
        return;
      }
      setCuil('');
      setEmail('');
      setPassword('');
      setPassword_copy('');
      setGlobalVariableValue({
        key: 'SHOW_CUIL_ERROR',
        value: '',
      });
      setGlobalVariableValue({
        key: 'SHOW_EMAIL_ERROR',
        value: '',
      });
      setGlobalVariableValue({
        key: 'SHOW_PASSWORD_ERROR',
        value: '',
      });
      setGlobalVariableValue({
        key: 'SHOW_PASSWORD_MISMATCH_ERROR',
        value: '',
      });
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);

  return (
    <ScreenContainer
      hasSafeArea={true}
      scrollable={false}
      style={StyleSheet.applyWidth(
        { backgroundColor: theme.colors.background, height: '100%' },
        dimensions.width
      )}
    >
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={'connecting'}
      />
      {/* PasswordHintView */}
      <>
        {!showPasswordHint ? null : (
          <View
            style={StyleSheet.applyWidth(
              {
                alignItems: 'center',
                backgroundColor: theme.colors.tooltip,
                height: '18%',
                justifyContent: 'center',
                left: '20%',
                opacity: 1,
                position: 'absolute',
                top: '50%',
                width: '65%',
                zIndex: 1,
              },
              dimensions.width
            )}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.strong,
                  marginLeft: 5,
                  marginRight: 5,
                  marginTop: 5,
                },
                dimensions.width
              )}
            >
              {
                'Longitud: 8 a 100 cars\nAl menos una letra mayscula\nAl menos una letra minscula\nAl menos 2 dgitos\nNo debe contener espacios'
              }
            </Text>
          </View>
        )}
      </>
      <KeyboardAwareScrollView
        enableAutomaticScroll={true}
        enableOnAndroid={true}
        enableResetScrollToCoords={true}
        extraHeight={200}
        style={StyleSheet.applyWidth({ height: '100%' }, dimensions.width)}
      >
        <View
          style={StyleSheet.applyWidth(
            {
              alignContent: 'space-around',
              alignItems: 'center',
              alignSelf: 'center',
              justifyContent: 'space-around',
              paddingTop: '5%',
              width: '90%',
            },
            dimensions.width
          )}
        >
          {/* app-logo */}
          <Image
            resizeMode={'contain'}
            source={Images.DigiSSTransp}
            style={StyleSheet.applyWidth(
              { height: 100, marginTop: 40, overflow: 'hidden', width: 300 },
              dimensions.width
            )}
          />
          <View
            style={StyleSheet.applyWidth(
              {
                alignContent: 'center',
                alignItems: 'center',
                marginBottom: 20,
                marginTop: 20,
              },
              dimensions.width
            )}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.strong,
                  fontFamily: 'System',
                  fontSize: 28,
                  fontWeight: '700',
                  opacity: 0.8,
                  textAlign: 'center',
                  textDecorationColor: theme.colors.primary,
                  textTransform: 'none',
                },
                dimensions.width
              )}
            >
              {'Bienvenido !'}
            </Text>
          </View>
        </View>

        <View
          style={StyleSheet.applyWidth(
            { justifyContent: 'center', minHeight: 30 },
            dimensions.width
          )}
        >
          <View
            style={StyleSheet.applyWidth(
              {
                alignContent: 'center',
                alignItems: 'center',
                justifyContent: 'center',
                minHeight: 30,
              },
              dimensions.width
            )}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.strong,
                  typography: theme.typography.subtitle2,
                },
                dimensions.width
              )}
            >
              {'Para crear tu cuenta ingres estos datos '}
            </Text>
          </View>
        </View>

        <Container
          backgroundImageResizeMode={'cover'}
          style={StyleSheet.applyWidth(
            { alignSelf: 'center', width: '90%' },
            dimensions.width
          )}
        >
          <View
            style={StyleSheet.applyWidth(
              { marginLeft: '2%', marginRight: '2%' },
              dimensions.width
            )}
          >
            <View>
              {/* Cuil */}
              <TextField
                editable={true}
                label={'CUIL'}
                onChangeText={newCuilValue => {
                  try {
                    setCuil(newCuilValue);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                style={StyleSheet.applyWidth(
                  { marginTop: '1%' },
                  dimensions.width
                )}
                type={'solid'}
                underlineColor={theme.colors.light}
                value={cuil}
              />
              <>
                {!SHOW_CUIL_ERROR ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {'El nmero de CUIL es incorrecto'}
                  </Text>
                )}
              </>
            </View>

            <View>
              {/* Email */}
              <TextField
                editable={true}
                keyboardType={'email-address'}
                label={'e-mail'}
                onChangeText={newEmailValue => {
                  try {
                    setEmail(newEmailValue);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                style={StyleSheet.applyWidth(
                  { marginTop: '1%' },
                  dimensions.width
                )}
                type={'solid'}
                underlineColor={theme.colors.light}
                value={email}
              />
              <>
                {!SHOW_EMAIL_ERROR ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {'Debe ser una direccin de email vlida'}
                  </Text>
                )}
              </>
            </View>

            <View>
              <View>
                {/* Password */}
                <TextField
                  editable={true}
                  keyboardType={'visible-password'}
                  label={'Contrasea'}
                  onChangeText={newPasswordValue => {
                    try {
                      setPassword(newPasswordValue);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={StyleSheet.applyWidth(
                    { marginTop: '1%' },
                    dimensions.width
                  )}
                  type={'solid'}
                  underlineColor={theme.colors.light}
                  value={password}
                />
                <View
                  style={StyleSheet.applyWidth(
                    { justifyContent: 'center', width: 0 },
                    dimensions.width
                  )}
                >
                  <>
                    {!false ? null : (
                      <IconButton
                        icon={'Ionicons/ios-help-circle-outline'}
                        size={24}
                      />
                    )}
                  </>
                </View>
              </View>
            </View>
            <>
              {!SHOW_PASSWORD_ERROR ? null : (
                <Text
                  numberOfLines={2}
                  style={StyleSheet.applyWidth(
                    {
                      color: theme.colors.error,
                      fontSize: 10,
                      marginBottom: 1,
                      textAlign: 'center',
                    },
                    dimensions.width
                  )}
                >
                  {'De 8 a 100 cars,  1 mayscula, 1 minscula, 2 dgitos'}
                </Text>
              )}
            </>
            <View>
              {/* PasswordCopy */}
              <TextField
                editable={true}
                keyboardType={'visible-password'}
                label={'Repetir contrasea'}
                onChangeText={newPasswordCopyValue => {
                  try {
                    setPassword_copy(newPasswordCopyValue);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                style={StyleSheet.applyWidth(
                  { marginBottom: '1%', marginTop: '1%' },
                  dimensions.width
                )}
                type={'solid'}
                underlineColor={theme.colors.light}
                value={password_copy}
              />
              <>
                {!SHOW_PASSWORD_MISMATCH_ERROR ? null : (
                  <Text
                    numberOfLines={1}
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.error,
                        fontSize: 10,
                        marginBottom: 1,
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {'Las contraseas no coinciden'}
                  </Text>
                )}
              </>
            </View>
          </View>

          <View
            style={StyleSheet.applyWidth({ marginTop: '4%' }, dimensions.width)}
          >
            <>
              {!errorMessage?.length ? null : (
                <Text
                  style={StyleSheet.applyWidth(
                    {
                      color: theme.colors.error,
                      fontSize: 12,
                      textAlign: 'center',
                    },
                    dimensions.width
                  )}
                >
                  {errorMessage}
                </Text>
              )}
            </>
          </View>
        </Container>

        <View
          style={StyleSheet.applyWidth(
            {
              alignItems: 'center',
              justifyContent: 'flex-end',
              marginBottom: '10%',
              marginTop: '5%',
              paddingLeft: '6%',
              paddingRight: '6%',
            },
            dimensions.width
          )}
        >
          <Touchable
            onPress={() => {
              try {
                navigation.navigate('Auth', { screen: 'IniciarSesionScreen' });
              } catch (err) {
                console.error(err);
              }
            }}
          >
            <Text
              style={StyleSheet.applyWidth(
                {
                  color: theme.colors.secondary,
                  paddingBottom: '10%',
                  textDecorationLine: 'underline',
                },
                dimensions.width
              )}
            >
              {'Ya tens cuenta,? Inici sesin!'}
            </Text>
          </Touchable>
          {/* Button Solid */}
          <Button
            onPress={() => {
              const handler = async () => {
                try {
                  if (cuilCheck(cuil)) {
                    return;
                  }
                  if (emailCheck(email)) {
                    return;
                  }
                  if (passwordCheck(password)) {
                    return;
                  }
                  const passwordMismatch = passwordCopyCheck(
                    password_copy,
                    password
                  );
                  if (passwordMismatch) {
                    return;
                  }
                  setLoading(true);
                  undefined;
                  const signupResponse = (
                    await DcSSApi.signupPOST(Constants, {
                      cuil: cuil,
                      email: email,
                      password: passEncrypt(password),
                    })
                  )?.json;
                  setLoading(false);
                  console.log(signupResponse);
                  const errorMessage = extractError(signupResponse);
                  setErrorMessage(errorMessage);
                  if (errorMessage?.length) {
                    return;
                  }
                  const emailVerified = signupResponse?.emailVerified;
                  console.log(emailVerified);
                  if (!emailVerified) {
                    return;
                  }
                  const username = signupResponse?.username;
                  setLoading(true);
                  const loginResponse = (
                    await DcSSApi.loginPOST(Constants, {
                      email: username?.toLowerCase(),
                      password: passEncrypt(password),
                    })
                  )?.json;
                  setLoading(false);
                  const loginError = extractError(loginResponse);
                  setErrorMessage(loginError);
                  if (loginError?.length) {
                    return;
                  }
                  const authToken = loginResponse?.accessToken;
                  const AUTH_HEADER = setGlobalVariableValue({
                    key: 'AUTH_HEADER',
                    value: 'Bearer ' + authToken,
                  });
                  console.log(AUTH_HEADER);
                  const refreshToken = loginResponse?.refreshToken;
                  const REFRESH_TOKEN = setGlobalVariableValue({
                    key: 'REFRESH_TOKEN',
                    value: refreshToken,
                  });
                  console.log(REFRESH_TOKEN);
                  const a_emp_id = loginResponse?.emp_id;
                  setGlobalVariableValue({
                    key: 'my_emp_id',
                    value: a_emp_id,
                  });
                  const a_emp_com_id = loginResponse?.emp_com_id;
                  setGlobalVariableValue({
                    key: 'my_emp_com_id',
                    value: a_emp_com_id,
                  });
                  navigation.navigate('Permissions', {
                    screen: 'BienvenidoScreen',
                  });
                } catch (err) {
                  console.error(err);
                }
              };
              handler();
            }}
            style={StyleSheet.applyWidth(
              {
                backgroundColor: theme.colors.primary,
                borderRadius: 10,
                color: theme.colors.background,
                fontFamily: 'System',
                fontWeight: '700',
                marginLeft: '6%',
                marginRight: '6%',
                marginTop: '5%',
                minHeight: 48,
                textAlign: 'center',
                width: '100%',
              },
              dimensions.width
            )}
            title={'Crear nuevo usuario'}
          />
        </View>
      </KeyboardAwareScrollView>
    </ScreenContainer>
  );
};

export default withTheme(NuevoUsuarioScreen);
