import React from 'react';
import * as CustomCode from '../CustomCode';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import Breakpoints from '../utils/Breakpoints';
import * as StyleSheet from '../utils/StyleSheet';
import useWindowDimensions from '../utils/useWindowDimensions';
import {
  CircleImage,
  Container,
  Divider,
  Icon,
  ScreenContainer,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { ActivityIndicator, Image, ScrollView, Text, View } from 'react-native';
import { Fetch } from 'react-request';

const InicioScreen = props => {
  const { theme, navigation } = props;
  const dimensions = useWindowDimensions();
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();
  const [modalLeftPadding, setModalLeftPadding] = React.useState(22);
  const [modalTopPadding, setModalTopPadding] = React.useState(6);
  const [showDrawer, setShowDrawer] = React.useState(false);
  const [showMenu, setShowMenu] = React.useState(false);
  const resetStackNav = () => {
    if (props.navigation.getState) {
      const navState = props.navigation.getState();

      if (navState.routes.length > 1)
        props.navigation.reset({
          index: 0,
          routes: [{ name: 'Main' }],
        });
    }
  };

  const initVars = () => {
    console.log(`Platform: ${JSON.stringify(CustomCode.Platform)}`);
    switch (CustomCode.Platform.OS) {
      case 'web':
        setModalTopPadding(6);
        setModalLeftPadding(12);
        break;

      case 'ios':
        setModalTopPadding(30);
        setModalLeftPadding(12);
        break;

      case 'android':
        setModalTopPadding(6);
        setModalLeftPadding(10);
        break;
    }
  };

  // Retorna un empty string
  const emptyString = any => {
    return '';
  };

  const menuItems = () => {
    return [
      {
        name: 'Consultas',
        icon: 'MaterialCommunityIcons/frequently-asked-questions',
        background: Images.UndrawSearch01,
        target: 'ConsultasScreen',
        enabled: true,
        visible: true,
        permission: 'Inicio:Consultas',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
      {
        name: 'Solicitudes',
        icon: 'FontAwesome/pencil-square-o',
        background: Images.UndrawRequest01,
        target: 'SolicitudesScreen',
        enabled: true,
        visible: true,
        permission: 'Inicio:Solicitudes',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
      {
        name: 'Fichada Mvil',
        icon: 'MaterialCommunityIcons/account-clock',
        background: Images.UndrawPunch01,
        target: 'ConsultasScreen',
        enabled: true,
        visible: true,
        permission: 'Inicio:FichadaMovil',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
      {
        name: 'Aprobaciones',
        icon: 'FontAwesome/check-square-o',
        background: Images.UndrawChecklist,
        target: 'AprobacionesScreen',
        enabled: true,
        visible: true,
        permission: 'Inicio:Aprobaciones',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
    ];
  };

  const checkPermsError = perms => {
    // console.log(`checkPermsError: ${JSON.stringify(perms)}`);
    // console.log(`perms error: ${perms.error}`);

    if (perms.error) return [];

    return perms;
  };

  const ParseCompanyConfigs = Variables => {
    const configs = Variables.CompanyConfigs;
    // console.log(`configs: ${JSON.stringify(configs, undefined, 2)}`);

    const timeout = configs.find(c => c.cnfName == 'LoginTimeout');

    if (timeout) {
      Variables.InactivityTimeout = Number(timeout.cnfCurValue);
      console.log(
        `Inactivity timeout set to ${Variables.InactivityTimeout} secs`
      );
    }

    // console.log(`Variables: ${JSON.stringify(Variables, undefined, 2)}`);
  };
  // ==============  INLINE API REST HOOKS ====================
  //
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  /*
const {
loading: employeeLoading,
data: employeeData,
error: employeeError,
} = CustomCode.useEmployeeFilteredGET({ 
emp_id: Constants['my_emp_id'],     
body: {
  "fields": {
    "emp_id": true,
    "emp_first_name": true,
    "emp_com_id": true,
  }
}
});

setVariable('firstName' , employeeData?.emp_first_name + ' (de API)');
console.log(`useEmployeeFilteredGET. data: ${JSON.stringify(employeeData)}`);

//
// ==============  INLINE API REST HOOKS END ================
*/
  const dcSSEmployeeTokenPATCH = DcSSApi.useEmployeeTokenPATCH();
  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        initVars();
        resetStackNav();
        const emp_photo_rslt = (
          await DcSSApi.employeePhotoGET(Constants, {
            emp_id: Constants['my_emp_id'],
          })
        )?.json;
        const a_emp_photo = emp_photo_rslt?.emp_photo;
        setGlobalVariableValue({
          key: 'emp_photo',
          value: a_emp_photo,
        });
        const permissions = (
          await DcSSApi.permissionsGET(Constants, {
            emp_id: Constants['my_emp_id'],
          })
        )?.json;
        setGlobalVariableValue({
          key: 'AppPermissions',
          value: checkPermsError(permissions),
        });
        console.log(Constants['AppPermissions']);
        const configs = (
          await DcSSApi.companyConfigsGET(Constants, {
            comId: Constants['my_emp_com_id'],
          })
        )?.json;
        setGlobalVariableValue({
          key: 'CompanyConfigs',
          value: configs,
        });
        ParseCompanyConfigs(Variables);
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer
      hasSafeArea={true}
      hasTopSafeArea={true}
      scrollable={false}
    >
      {/* Fetch Screen (InicioScreen) */}
      <DcSSApi.FetchEmployeeBasicGET
        emp_id={Constants['my_emp_id']}
        handlers={{
          onData: fetchScreenInicioScreenData => {
            const handler = async () => {
              try {
                const company_rslt = (
                  await DcSSApi.companyGET(Constants, {
                    com_id: Constants['my_emp_com_id'],
                  })
                )?.json;
                const a_company_name = company_rslt?.com_description;
                setGlobalVariableValue({
                  key: 'company_name',
                  value: a_company_name,
                });
              } catch (err) {
                console.error(err);
              }
            };
            handler();
          },
        }}
      >
        {({ loading, error, data, refetchEmployeeBasic }) => {
          const fetchScreenInicioScreenData = data?.json;
          if (loading) {
            return <ActivityIndicator />;
          }

          if (error || data?.status < 200 || data?.status >= 300) {
            return <ActivityIndicator />;
          }

          return (
            <>
              <CustomCode.DigicardScreenHeader
                {...props}
                left={{
                  icon: 'Feather/menu',
                  onPress: () => setShowDrawer(true),
                }}
                right={{
                  icon: 'Feather/bell',
                  nav: 'Notifications',
                  screen: 'NotificacionesScreen',
                }}
              />
              <View
                style={StyleSheet.applyWidth(
                  { marginLeft: 12, marginRight: 12, zIndex: -1 },
                  dimensions.width
                )}
              >
                <View>
                  <View
                    style={StyleSheet.applyWidth(
                      { marginBottom: 10, marginTop: 10 },
                      dimensions.width
                    )}
                  >
                    <Touchable
                      onPress={() => {
                        try {
                          setShowMenu(!showMenu);
                        } catch (err) {
                          console.error(err);
                        }
                      }}
                    >
                      <Text
                        style={StyleSheet.applyWidth(
                          {
                            color: theme.colors.secondary,
                            fontFamily: 'Montserrat_600SemiBold',
                            textAlign: 'right',
                          },
                          dimensions.width
                        )}
                      >
                        {Constants['company_name']}
                      </Text>
                    </Touchable>
                  </View>

                  <View
                    style={StyleSheet.applyWidth(
                      {
                        alignContent: 'space-between',
                        flexDirection: 'row',
                        justifyContent: 'space-between',
                      },
                      dimensions.width
                    )}
                  >
                    <View
                      style={StyleSheet.applyWidth(
                        { alignItems: 'flex-start', width: '75%' },
                        dimensions.width
                      )}
                    >
                      <Text
                        allowFontScaling={true}
                        style={StyleSheet.applyWidth(
                          {
                            alignSelf: 'flex-start',
                            color: theme.colors.strong,
                            fontFamily: 'System',
                            fontSize: 24,
                            fontWeight: '600',
                          },
                          dimensions.width
                        )}
                      >
                        {'Hola '}
                        {fetchScreenInicioScreenData?.emp_first_name}
                        {'!'}
                      </Text>

                      <Text
                        style={StyleSheet.applyWidth(
                          {
                            color: theme.colors.primary,
                            fontFamily: 'System',
                            fontSize: 16,
                            fontWeight: '600',
                            marginTop: 8,
                          },
                          dimensions.width
                        )}
                      >
                        {'Bienvenido/a a Digicard Self Service'}
                      </Text>
                    </View>
                    <CircleImage
                      size={80}
                      source={{ uri: `${Constants['emp_photo']}` }}
                    />
                  </View>
                  <View />
                </View>

                <View
                  style={StyleSheet.applyWidth(
                    {
                      alignItems: 'center',
                      flexDirection: 'row',
                      justifyContent: 'center',
                      marginBottom: 10,
                      marginTop: 10,
                    },
                    dimensions.width
                  )}
                >
                  <Icon
                    color={theme.colors.optionSelected}
                    name={'MaterialCommunityIcons/phone-in-talk'}
                    size={24}
                    style={StyleSheet.applyWidth(
                      { marginRight: 6 },
                      dimensions.width
                    )}
                  />
                  <Text
                    style={StyleSheet.applyWidth(
                      {
                        color: theme.colors.optionSelected,
                        fontFamily: 'System',
                        fontSize: 16,
                        fontWeight: '600',
                        textAlign: 'center',
                      },
                      dimensions.width
                    )}
                  >
                    {'Tu linea directa con RRHH'}
                  </Text>
                </View>
              </View>

              <View
                style={StyleSheet.applyWidth(
                  {
                    flex: 1,
                    marginBottom: 10,
                    marginLeft: 12,
                    marginRight: 12,
                    paddingBottom: 25,
                  },
                  dimensions.width
                )}
              >
                <Utils.CustomCodeErrorBoundary>
                  {CustomCode.renderMenuItems(
                    menuItems(),
                    Constants['AppPermissions'],
                    props
                  )}
                </Utils.CustomCodeErrorBoundary>
              </View>
              {/* OptionsModal */}
              <View removeClippedSubviews={false}>
                <CustomCode.SafeAreaView>
                  <CustomCode.rnModal
                    isVisible={showDrawer}
                    animationIn={'slideInLeft'}
                    animationOut={'slideOutLeft'}
                    backdropColor={'white'}
                    backdropOpacity={0.1}
                    onBackdropPress={() => setShowDrawer(false)}
                    onSwipeComplete={() => setShowDrawer(false)}
                    swipeDirection={'left'}
                    swipeThreshold={50}
                    style={{ margin: 0 }}
                    hideModalContentWhileAnimating={true}
                  >
                    <Container
                      style={{
                        flex: 1,
                        width: '85%',
                        alignSelf: 'flex-start',
                        opacity: 0.95,
                        paddingTop: modalTopPadding,
                        paddingLeft: modalLeftPadding,
                        backgroundColor: theme.colors.OptionMenu,
                        borderRadius: 0,
                        borderTopRightRadius: 16,
                        borderBottomRightRadius: 16,
                      }}
                      backgroundImageResizeMode={'cover'}
                    >
                      <Container
                        backgroundImageResizeMode={'cover'}
                        style={StyleSheet.applyWidth(
                          { borderRadius: 1, height: '100%', width: '70%' },
                          dimensions.width
                        )}
                      >
                        <View
                          style={StyleSheet.applyWidth(
                            { borderRadius: 23 },
                            dimensions.width
                          )}
                        >
                          <Touchable
                            onPress={() => {
                              try {
                                setShowDrawer(false);
                              } catch (err) {
                                console.error(err);
                              }
                            }}
                            style={StyleSheet.applyWidth(
                              { marginBottom: 5, marginTop: 5 },
                              dimensions.width
                            )}
                          >
                            <Icon
                              color={theme.colors.background}
                              name={'MaterialCommunityIcons/backburger'}
                              size={32}
                            />
                          </Touchable>

                          <Touchable
                            onPress={() => {
                              try {
                                setShowDrawer(false);
                                navigation.navigate('Main', {
                                  screen: 'AboutScreen',
                                });
                              } catch (err) {
                                console.error(err);
                              }
                            }}
                            style={StyleSheet.applyWidth(
                              { marginBottom: 5, marginTop: 25 },
                              dimensions.width
                            )}
                          >
                            <View
                              style={StyleSheet.applyWidth(
                                { flexDirection: 'row', paddingBottom: 10 },
                                dimensions.width
                              )}
                            >
                              <Icon
                                color={theme.colors.background}
                                name={'AntDesign/infocirlceo'}
                                size={24}
                                style={StyleSheet.applyWidth(
                                  { marginRight: 10 },
                                  dimensions.width
                                )}
                              />
                              <Text
                                style={StyleSheet.applyWidth(
                                  {
                                    color: theme.colors.background,
                                    fontFamily: 'System',
                                    fontSize: 18,
                                    fontWeight: '700',
                                    marginTop: 2,
                                  },
                                  dimensions.width
                                )}
                              >
                                {'Acerca de...'}
                              </Text>
                            </View>
                          </Touchable>

                          <Touchable
                            onPress={() => {
                              const handler = async () => {
                                try {
                                  const tokenRslt = (
                                    await dcSSEmployeeTokenPATCH.mutateAsync({
                                      emp_id:
                                        fetchScreenInicioScreenData?.emp_id,
                                      emp_token: '',
                                    })
                                  )?.json;
                                  setGlobalVariableValue({
                                    key: 'AUTH_HEADER',
                                    value: emptyString(tokenRslt),
                                  });
                                  setShowDrawer(false);
                                  navigation.navigate('Auth', {
                                    screen: 'IniciarSesionScreen',
                                  });
                                } catch (err) {
                                  console.error(err);
                                }
                              };
                              handler();
                            }}
                            style={StyleSheet.applyWidth(
                              { marginBottom: 5, marginTop: 5 },
                              dimensions.width
                            )}
                          >
                            <View
                              style={StyleSheet.applyWidth(
                                { flexDirection: 'row', paddingBottom: 10 },
                                dimensions.width
                              )}
                            >
                              <Icon
                                color={theme.colors.background}
                                name={'SimpleLineIcons/logout'}
                                size={24}
                                style={StyleSheet.applyWidth(
                                  { marginRight: 10 },
                                  dimensions.width
                                )}
                              />
                              <Text
                                style={StyleSheet.applyWidth(
                                  {
                                    color: theme.colors.background,
                                    fontFamily: 'System',
                                    fontSize: 18,
                                    fontWeight: '700',
                                    marginTop: 2,
                                  },
                                  dimensions.width
                                )}
                              >
                                {'Cerrar sesin'}
                              </Text>
                            </View>
                          </Touchable>
                        </View>
                      </Container>
                    </Container>
                  </CustomCode.rnModal>
                </CustomCode.SafeAreaView>
              </View>
            </>
          );
        }}
      </DcSSApi.FetchEmployeeBasicGET>
    </ScreenContainer>
  );
};

export default withTheme(InicioScreen);
